.event-cart{
    font-size: 0;
    margin: 0 auto;
    width: 1180px;
    padding-top: 193px;
    color: #2a2a2a;
    @media(max-width: 1320px){
        width: 80%;
    }
    @media(max-width: 780px){
        margin: 0 10px;
    }
    @media(max-width: 600px){
        width: calc(100% - 20px);
    }
    .caption{
        padding-left: 5px;
        margin-bottom: 50px;
        p{
            color: #2a2a2a;
            font-size: 35px;
            font-weight: 600;
            // line-height: 40px;
        }
    }
    .event{
        margin-bottom: 97px;
        .cart{
            position: relative;
            padding-left: 63px;
            display: inline-block;
            width: 235px;
            height: 253px;
            margin-right: 127px;
            vertical-align: top;
            .cancel{
                position: absolute;
                width: 14px;
                height: 14px;
                top: 120px;
                left: 5px;
                cursor: pointer;
                transition: 0.7s;
                &:hover{
                    opacity: 0.6;
                    transition: 0.7s;
                }
            }
        }
        .right-part{
            display: inline-block;
            .price{
                // padding-left: 1px;
                width: 421px;
                position: relative;
                h1{ 
                    margin: 0;
                    display: inline-block;
                    font-size: 18px;
                    font-weight: 600;
                    line-height: 22px;
                    margin-bottom: 12px;
                }
                p{
                    color: #949494;
                    font-size: 16px;
                    font-weight: 400;
                    // line-height: 26px;
                    margin-bottom: 9px;
                    &:last-of-type{
                        margin-bottom: 64px;
                    }
                }
                .button{

                    cursor: pointer;
                    display: inline-block;
                    position: absolute;
                    top: 0;
                    right: 0;
                    width: 120px;
                    height: 55px;
                    border-radius: 3px;
                    background-image: linear-gradient(270deg, #8889f2 0%, #3183fb 100%);
                    p{
                        
                        text-align: center;
                        line-height: 55px;
                        color: #ffffff;
                        font-size: 14px;
                        font-weight: 400;
                        text-transform: uppercase;
                    }
                    @media(max-width: 450px){
                        display: block;
                        position: static;
                        margin-bottom: 20px;
                    }
                }
            }
            .discount{
                position: relative;
                width: 350px;
                margin-bottom: 31px;
                @media(max-width: 450px){
                    width: 100%;
                }
                input{
                    outline: none;
                    display: inline-block;
                    width: 229px;
                    height: 55px;
                    border: 1px solid rgba(49, 131, 251, 0.4);
                    color: #3183fb;
                    font-size: 14px;
                    line-height: 55px;
                    padding-left: 25px;
                    font-weight: 600;
                    background: transparent;
                    &::placeholder{
                        color: #3183fb;
                    }
                    &:focus+p{
                        color: transparent;
                    }
                }
                p{
                    cursor: pointer;
                    display: inline-block;
                    position: absolute;
                    top: 14px;
                    right: 10px;
                    opacity: 0.4;
                    color: #949494;
                    font-size: 14px;
                    text-transform: uppercase;
                    &.code{
                        padding-left: 25px;
                        display: block;
                        top: 0;
                        left: 0;
                        line-height: 55px;
                        color: #3183fb;
                        font-weight: 600;
                        z-index: -5;
                        opacity: 0.4;
                    }
                }
                img{
                    cursor: pointer;
                    z-index: -5;
                    position: absolute;
                    top: 16px;
                    right: 36px;
                    opacity: 0;
                    @media(max-width: 450px){
                        right: 30%;
                    }
                }
                &.active{
                    p{
                        display: none;
                        opacity: 0;
                        &.code{
                            display: block;
                            opacity: 1;
                        }
                    }
                    img{
                        z-index: 5;
                        opacity: 1;
                        &:hover{
                            opacity: 0.6;
                            transition: 0.7s;
                        }
                    }
                    input{
                        border-color: rgba(49, 131, 251, 1);
                    }
                }
            }
            p.tot{
                color: #3183fb;
                font-size: 18px;
                font-weight: 600;
                // line-height: 25px;
            }
        }
    }
    .total{
        position: relative;
        margin-top: 117px;
        padding-left: 63px;
        margin-bottom: 229px;
        @media(max-width: 780px){
            padding-left: 0;
        }
        .right-part-tot{
            display: inline-block;
            p{
                color: #949494;
                font-size: 16px;
                font-weight: 400;
                letter-spacing: 0.1px;
                // line-height: 26px;
            }
            h1{
                margin: 0;
                color: #2a2a2a;
                font-size: 25px;
                font-weight: 600;
                line-height: 36px;
                padding-left: 1px;
            }
        }
        .button{
            cursor: pointer;
            position: absolute;
            top: -14px;
            right: 5px;
            width: 200px;
            height: 85px;
            border-radius: 5px;
            background-image: linear-gradient(270deg, #8889f2 0%, #3183fb 100%);
            p{
                color: #ffffff;
                font-size: 14px;
                font-weight: 400;
                line-height: 85px;
                text-transform: uppercase;
                text-align: center;
            }
            @media(max-width: 600px){
                position: static;
                margin: 30px auto;
            }
        }
    }
}


