.wrapper-filter{
    margin-top: 97px;
    .filter-head{
        opacity: 1;
        transition: .1s ease-out;
        &.hidden{
            opacity: 0;

        }
        h2{
            text-align: center;
            color: #2a2a2a;
            font-family: Helvetica;
            font-size: 35px;
            font-weight: 600;
            line-height: 40px;
            padding-right: 1px;
        }
    }
    .filter-w{
        border-radius: 4px;
        background-color: #e8f1ff;
        max-width: 875px;
        padding-right: 3px;
        margin:0 auto;
        transition: .3s;
        margin-top: 53px;
        &.scroll{
            max-width: 100%;
            width: 100%;
            border-radius: 0;
            position: fixed;
            top: 47px;
            left: 0;
            right: 0;
            z-index: 5;
        }
        .filter-item-w{
            max-width: 873px;
            margin:0 auto;
            font-size: 0;
            padding: 23px 0;
            @media(max-width:870px){
                text-align: center;
            }
            .filter-item{
                display: inline-block;
                padding: 0 50px 0 52px;
                line-height: 0;
                position: relative;
                cursor: pointer;
                text-align: left;
                @media(max-width: 980px){
                    padding: 0 5.905% 0 6.14%;
                }
                &.start{
                    padding: 0;
                    max-width: 236px;
                    width: 100%;
                    @media(max-width: 960px) {
                        width: 27.065%;
                    }
                    @media(max-width: 780px) {
                        width: 33%;
                    }
                }
                &.end{
                    padding: 0;
                    max-width: 234px;
                    width: 100%;
                    @media(max-width: 960px) {
                        width: 26.836%;
                    }
                    @media(max-width: 780px) {
                        width: 33%;
                    }
                }
                &.name-i{
                    padding-right: 1px;
                    padding: 0 52px 0 52px;
                    @media(max-width: 960px) {
                        width: 16.17%;
                    }
                    @media(max-width: 780px) {
                        display: none;
                    }
                    .name{

                        p{
                           opacity: 1;
                           color: #3183fb;
                       }
                   }
               }
               @media(max-width: 870px){
                padding: 0 15px;
                &::before{
                    display: none;
                }
                &::after{
                    right: 15px;
                }
            }
            @media(max-width: 470px){
                padding: 0 7px;
            }
            &.name{
                @media(max-width:870px){
                    display: none;
                }
            }
            @media(max-width: 470px){
                &::after{
                    right:7px;
                }
            }
            &:hover{
                .name{
                    p{

                        color: #3183fb;
                        opacity: 1;
                    }
                }
            }
            &::before{
                content: "";
                position: absolute;
                bottom: 0;
                top: 0;
                right: 0;
                width: 1px;
                height: 40px;
                background: #c7ddfe;
            }
            &:last-child{
                &::before{
                    display: none;
                }
            }
            .name{
                display: inline-block;
                vertical-align: middle;
                width: 100%;
                @media(max-width:870px){
                    vertical-align: middle;
                }
                &:hover{
                    input{
                        color: #3183fb;
                        opacity: 1;
                    }
                } 

                input, p{
                    transition: .3s;
                    opacity: 0.5;
                    color: #2a2a2a;
                    font-family: Helvetica;
                    font-size: 14px;
                    font-weight: 600;
                    line-height: 40px;
                    text-transform: uppercase;
                    width: 100%;
                    border: none;
                    background: transparent;
                    outline: none;

                    &:focus{
                        color: #3183fb;
                        opacity: 1;
                    }
                    @media(max-width:870px){
                        line-height: 20px;
                    }
                }
                input{
                    padding: 0 52px 0 81px;
                    position: relative;
                    cursor: pointer;
                    @media(max-width: 980px){
                        padding: 0 22.3% 0 34.74%;
                    }
                    @media(max-width: 620px){
                        padding: 0 22.3% 0 22.74%;
                    }@media(max-width: 480px){
                        padding: 0 22.3% 0 5%;
                    }
                }

            }
        }
    }
}
.data-p{
    .pics{
        position: absolute;
        top: 0;
        bottom: 0;
        left: 52px;
        margin: auto;
        height: 25px;
        width: 23px;
        @media(max-width:870px){
            width: 18px;
            left: 21%;

            img{
                width: 100%;
                margin: 0;
            }
        }
        @media(max-width: 620px){
           left: 8%;
       }
       @media(max-width: 480px){
        display: none;
       }
       img{
        margin-right: 10px;
        vertical-align: middle;
    }
}
.name{
    width: 98px;
    @media(max-width: 470px){
        width: 78px;
    }
    p{}
}
}
.categ{
    animation: over 7s forwards;
    width: 29.818%;
    @media(max-width: 960px){
        width: 29.818%;
    }
    .name{
        width: 158px;
        display: inline-block;
        @media(max-width: 470px){
            width: 94px;
        }
    }
    &.active{
        overflow: inherit;
        .drope-categ{
            clip-path: polygon(100% 0, 100% 100%, 0 100%, 0 0);
        }
    }
    .drope-categ{
        transition: 0.7s;
        // opacity: 0;
        position: absolute;
        top: 63px;
        left: -10px;
        z-index: 3;
        width: 241px;
        transition: .7s cubic-bezier(0.705, 0.180, 0.210, 1.030);
        clip-path: polygon(100% 0, 100% 0, 0 0, 0 0);
        padding: 0 10px 10px;
        .w-d-c{
            box-shadow: 0 2px 16px rgba(49, 131, 251, 0.17);
            .drope-element{
                background-color: #ffffff;
                &:hover{
                    background-color: #e8f1ff;
                    p{
                        color: #2a2a2a;
                    }
                }
                p{
                    padding-left: 28px;
                    line-height: 50px;
                    color: #949494;
                    font-size: 14px;
                    font-weight: 600;
                }
            }
        }
    }
}
.drop{
    position: relative;

    &::after{
        content: "";
        width: 10px;
        height: 6px;
        position: absolute;
        top: 5px;
        bottom: 0;
        right: 52px;
        margin: auto 0;
        background: url(../img/pics/arrow.png);
    }
    &:last-child{
        &::after{
            right: 50px;
        }
    }
}
}


