.my-events-wrap{
    padding-top: 193px;
    padding-right: 1px;
    margin-bottom: 174px;
    .my-events{
        margin: 0 auto;
        width: 1170px;
        font-size: 0;
        @media(max-width: 1300px){
            width: 88%;
        }
        @media(max-width: 780px){
            margin: 0 10px;
            width: calc(100% - 20px);
        }
        .caption{
            position: relative;
            padding-left: 50px;
            margin-bottom: 51px;
            p{
                color: #2a2a2a;
                font-size: 35px;
                font-weight: 600;
            }
            a{
                content: "";
                position: absolute;
                top: 13px;
                left: 9px;
                width: 11px;
                height: 19px;
                background: url("../img/pics/arrow-left.png") no-repeat 0 0;
            }
        }
        .button-wrap{
            font-size: 0;
            margin-bottom: 50px;
            .button-bar{
                display: inline-block;
                width: 441px;
                height: 85px;
                border-radius: 5px;
                background-color: #e8f1ff;
                margin-left: 1px;
                @media(max-width: 510px){
                    width: 100%;
                }
                .button{
                    cursor: pointer;
                    display: inline-block;
                    width: 29%;
                    padding: 22px 0;
                    transition: 0.5s;
                    &:hover{
                        opacity: 0.6;
                    }
                    p{
                        border-right: 1px solid #c7ddfe;
                        line-height: 40px;
                        text-align: center;
                        opacity: 0.5;
                        color: #2a2a2a;
                        font-size: 14px;
                        font-weight: 600;
                        text-transform: uppercase;
                        &.active{
                            opacity: 1;
                            color: #949494;
                        }
                    }
                    &.past{
                        width: 32%;
                        p{
                            padding-left: 2px;
                        }
                    }
                    &.current{
                        width: 39%;
                        p{
                            border: none;
                        }
                    }
                }
            }
        }
        .button-draft{
            cursor: pointer;
            margin-right: 1px;
            display: inline-block;
            width: 177px;
            height: 85px;
            border-radius: 5px;
            background-color: #e8f1ff;
            float: right;
            
            &:hover{
                p{
                    opacity: 0.6;
                }
            }
            @media(max-width: 740px){
                float: none;
                display: block;
                margin-top: 30px;
            }
            p{
                transition: 0.5s;
                line-height: 85px;
                text-align: center;
                color: rgb(49, 131, 251);
                font-size: 14px;
                font-weight: 600;
                text-transform: uppercase;
            }
        }
        form{
            font-size: 0;
            display: inline-block;
            margin-right: 30px;
            &:last-of-type{
                margin-right: 0;
            }
            input{
                width: 0;
                position: absolute;
                z-index: -10;
                opacity: 0;
            }
            input+label{
                display: inline-block;
                width: 370px;
                height: 547px;
                cursor: pointer;
                border-radius: 4px;
                border: 1px dashed rgba(49, 131, 251, 0.4);
                color: #2a2a2a;
                font-size: 14px;
                font-weight: 600;
                line-height: 12px;
                text-transform: uppercase;
                text-align: center;
                margin-bottom: 40px;
                
                padding-top: 242px;

                img{
                    opacity: 1;
                    margin-bottom: 17px;
                }
                p{
                    padding-right: 2px;
                }
                a{
                    text-decoration: none;
                    display: block;
                    margin: 130px auto 0;
                    width: 99px;
                    height: 55px;
                    border-radius: 3px;
                    background-image: linear-gradient(270deg, #8889f2 0%, #3183fb 100%);
                    p{
                        color: #ffffff;
                        font-weight: 400;
                        line-height: 55px;
                        padding-left: 3px;
                    }
                }
            }
        }
    }
}