
.event-wrapper{
    padding-top: 100px;
    font-size: 0;
    .ev-header{
        position: relative;
        margin-bottom: 100px;
        .ev-back{
            img{
                width: 100%;
            }
        }
        .ev-name{
            position: absolute;
            bottom: 59px;
            left: 100px;
            @media(max-width: 1100px){
                bottom: 3vw;
                left: 15px;
            }
            @media(max-width: 550px){
                bottom: 10vw;
            }
            p{
                color: rgb(255, 255, 255);
                font-size: 35px;
                font-weight: 600;
                margin-bottom: 28px;
                // line-height: 40px;
                @media(max-width: 550px){
                    margin-bottom: 0;
                    font-size: 22px;
                }
            }
            h1{
                margin: 0;
                color: rgb(255, 255, 255);
                font-size: 55px;
                font-weight: 600;
                @media(max-width: 1100px){
                    font-size: 35px;
                }
                @media(max-width: 550px){
                    font-size: 28px;
                }
                // line-height: 60px;
            }
            &:after{
                content: "";
                position: absolute;
                top: 55px;
                left: 0;
                width: 86px;
                height: 1px;
                opacity: 0.5;
                background: rgb(255, 255, 255);
                @media(max-width: 550px){
                    display: none;
                }

            }
        }
        .like-it{
            position: absolute;
            padding-left: 68px;
            bottom: 74px;
            right: 84px;
            transition: 0.7s;
            &:hover{
                opacity: 0.6;
            }
            @media(max-width: 1100px){
                bottom: 3vw;
                right: 15px;
            }
            @media(max-width: 550px){
                right: auto;
                left: 15px;
                padding-left: 35px;
            }
            p{
                color: rgb(255, 255, 255);
                font-size: 14px;
                font-weight: 600;
                line-height: 85px;
                text-transform: uppercase;
                transition: 0.7s;
                @media(max-width: 550px){
                    line-height: 30px;
                }
            }
            img{
                position: absolute;
                top: 32px;
                left: 25px;
                transition: 0.7s;
                @media(max-width: 550px){
                    left: 0;
                    top: 5px;
                }
            }
            &:after{
                @media(max-width: 700px){
                    display: none;
                }
                content: "";
                position: absolute;
                top: -3px;
                left: 0;
                width: 1px;
                height: 85px;
                opacity: 0.5;
                background: rgb(255, 255, 255);
            }
        }
    }



    .ev-content{
        width: 1170px;
        margin: 0 auto;
        @media(max-width: 1360px){
            width: calc(100% - 30px);
        }
        .ev-left-column{
            display: inline-block;
            width: 770px;
            @media(max-width: 920px){
                width: 100%;
            }
            .ev-right-column{
                display: none;
                @media(max-width: 1200px){
                    display: block;
                }
            }
            .caption{
                width: 566px;
                position: relative;
                padding-left: 300px;
                height: 399px;
                margin-bottom: 47px;
                @media(max-width: 650px){
                    width: 60%;
                    height: auto;
                    margin-bottom: 30px;
                    padding-left: 0;
                }
                img{
                    width: 270px;
                    position: absolute;
                    top: 0;
                    left: 0;
                    @media(max-width: 650px){
                        width: 100%;
                        position: static;
                        margin-bottom: 30px;
                    }
                }
                p{
                    display: inline-block;
                    color: rgb(148, 148, 148);
                    font-size: 14px;
                    font-weight: 600;
                    // line-height: 14px;
                    &.category{
                        text-transform: uppercase;
                        line-height: 13px;
                        margin-bottom: 20px;
                        letter-spacing: 0.06px;

                    }
                    &.name{
                        width: 250px;
                        color: rgb(49, 131, 251);
                        font-size: 18px;
                        line-height: 26px;
                        margin-bottom: 2px;
                    }
                    &.address{
                        color: rgb(42, 42, 42);
                        font-size: 16px;
                        font-weight: 400;
                        letter-spacing: 0.09px;
                        margin-bottom: 59px;
                    }
                }
                .data{
                    position: relative;
                    margin-bottom: 13px;
                    img{
                        width: auto;
                        position: absolute;
                        top: -3px;
                        left: 0;
                    }
                    p{
                        padding-left: 37px;
                        color: rgb(137, 137, 242);
                        font-size: 14px;
                        font-weight: 600;
                        // line-height: 14px;
                        // letter-spacing: 0.7px;
                        text-transform: uppercase;
                    }
                }
                .start{
                    position: relative;
                    img{
                        width: auto;
                        position: absolute;
                        top: 0;
                        left: 1px;
                    }
                    p{
                        padding-left: 37px;
                        color: rgb(137, 137, 242);
                        font-size: 14px;
                        font-weight: 600;
                        // line-height: 14px;
                        text-transform: uppercase;
                        // letter-spacing: 0.8px;
                    }
                }
            }
            .description{
                p{
                    opacity: 0.5;
                    color: rgb(42, 42, 42);
                    font-size: 16px;
                    font-weight: 400;
                    margin-bottom: 26px;
                    line-height: 26px;
                    letter-spacing: 0.01px;
                }
            }
            img.event-photo{
                margin-top: 25px;
                width: 100%;
            }
            .img-bar{
                margin-top: 4px;
                margin-bottom: 47px;
                img{
                    margin-right: 4px;
                    width: 13%;
                }
            }
            p.descript{
                opacity: 0.5;
                color: rgb(42, 42, 42);
                font-size: 16px;
                font-weight: 400;
                line-height: 26px;
                margin-bottom: 152px;
            }
            .comments-wrap{
                .add-comments{
                    margin-bottom: 79px;
                    .com-history{
                        width: 770px;
                        @media(max-width: 920px){
                            width: 100%;
                        }
                        height: 85px;
                        border-radius: 4px 4px 0 0;
                        background-color: rgb(232, 241, 255);
                        .history{
                            padding-left: 25px;
                            display: inline-block;
                            p{
                                position: relative;
                                color: rgb(49, 131, 251);
                                font-size: 14px;
                                font-weight: 600;
                                line-height: 85px;
                                text-transform: uppercase;
                                &:after{
                                    content: "";
                                    position: absolute;
                                    top: 22px;
                                    left: 120px;
                                    width: 1px;
                                    height: 40px;
                                    opacity: 1;
                                    background: rgb(199, 221, 254);
                                }
                            }
                        }
                        .oldest{
                            position: relative;
                            display: inline-block;
                            float: right;
                            padding-right: 60px;
                            p{
                                color: rgb(42, 42, 42);
                                font-size: 14px;
                                font-weight: 600;
                                opacity: 0.5;
                                line-height: 85px;
                                text-transform: uppercase;
                            }
                            img.arrow-down{
                                transform: rotate(90deg);
                                position: absolute;
                                top: 38px;
                                right: 32px;
                            }
                            .drope-comments{
                                display: none;
                                transition: 0.7s;
                                opacity: 1;
                                position: absolute;
                                top: 85px;
                                left: -50px;
                                z-index: 3;
                                width: 167px;
                                overflow: hidden;
                                padding-top: 0;
                                transition: .7s cubic-bezier(0.705, 0.180, 0.210, 1.030);
                                // clip-path: polygon(100% 0, 100% 0, 0 0, 0 0);
                                .d-c-w{
                                    background: rgba(255,255,255,1);
                                    box-shadow: 0 2px 16px rgba(49, 131, 251, 0.17);
                                    
                                    .drope-element{
                                        // background-color: #ffffff;
                                        transition: opacity .2s ease-in .7s;
                                        // opacity: 0;
                                        overflow: hidden;
                                        &:hover{
                                            background-color: #e8f1ff;
                                            p{
                                                color: #2a2a2a;
                                            }
                                        }
                                        p{
                                            transition: color .3s ease;
                                            padding-left: 50px;
                                            line-height: 50px;
                                            color: #949494;
                                            font-size: 14px;
                                            font-weight: 600;
                                        }
                                    }
                                }
                            }
                        }
                    }
                    .new-comments{
                        position: relative;
                        width: 770px;
                        @media(max-width: 920px){
                            width: 100%;
                        }
                        min-height: 85px;
                        border-radius: 0 0 4px 4px;
                        border: 1px solid rgb(199, 221, 254);
                        border-top: none;
                        .add-new-com{
                            font-size: 14px;
                            padding: 29px 24px;
                            line-height: 25px;
                            font-size: 14px;
                            outline: none;
                            font-weight: 600;
                            color: rgb(49, 131, 251);
                        }
                        p{
                            z-index: -5;
                            padding-left: 24px;
                            position: absolute;
                            top: 0;
                            left: 0;
                            color: rgb(49, 131, 251);
                            font-size: 14px;
                            font-weight: 600;
                            opacity: 0;
                            line-height: 83px;
                            text-transform: uppercase;
                            &.add-new-com-plh{
                                opacity: 0.4;
                            }
                        }
                    }
                    .btn{
                        margin-top: 24px;
                        float: right;
                        display: inline-block;
                        width: 120px;
                        height: 55px;
                        border-radius: 3px;
                        p{
                            color: rgb(148, 148, 148);
                            font-size: 14px;
                            font-weight: 400;
                            line-height: 55px;
                            text-align: center;
                            text-transform: uppercase;
                        }
                        &.cancel{
                            cursor: pointer;
                            background: transparent;
                            margin-right: 5px;
                            transition: 0.7s;
                            &:hover{
                                opacity: 0.6;
                            }
                            p{
                                color: rgb(148, 148, 148);
                            }
                        }
                        &.post{
                            cursor: pointer;
                            background-image: linear-gradient(270deg, rgb(136, 137, 242) 0%, rgb(49, 131, 251) 100%);
                            p{
                                color: rgb(255, 255, 255);
                            }
                        }
                    }
                
                    &:after{
                        content: "";
                        display: table;
                        clear: both;
                    }
                }
                .comments{
                    padding-left: 70px;
                    position: relative;
                    margin-bottom: 155px;
                    img{
                        position: absolute;
                        top: 1px;
                        left: 0;
                    }
                    .ac-name{
                        display: inline-block;
                        position: relative;
                        padding-right: 31px;
                        margin-bottom: 16px;
                        p{
                            opacity: 1;
                            color: rgb(42, 42, 42);
                            font-size: 18px;
                            font-weight: 700;
                            line-height: 25px;
                        }
                        img.dot{
                            top: 12px;
                            left: auto;
                            right: 12px;
                        }
                    }
                    .time-ago{
                        display: inline-block;
                        p{
                            opacity: 1;
                            color: rgb(148, 148, 148);
                            font-size: 14px;
                            font-weight: 400;
                            line-height: 25px;
                        }
                    }
                    p.post-text{
                        margin-bottom: 23px;
                        opacity: 0.5;
                        color: rgb(42, 42, 42);
                        font-size: 14px;
                        font-weight: 400;
                        line-height: 24px;
                    }
                    .reply{
                        cursor: pointer;
                        display: inline-block;
                        margin-right: 24px;
                        transition: 0.5s;
                        &:hover{
                            opacity: 0.6;
                        }
                        p{
                            color: rgb(49, 131, 251);
                            font-size: 14px;
                            font-weight: 400;
                            // line-height: 14px;
                        }
                    }
                    .like{
                        cursor: pointer;
                        display: inline-block;
                        transition: 0.5s;
                        &:hover{
                            opacity: 0.6;
                        }
                        p{
                            color: rgb(49, 131, 251);
                            font-size: 14px;
                            font-weight: 400;
                        }
                    }
                }
            }
        }



        .ev-right-column{
            position: relative;
            float: right;
            // padding-right: 69px;
            padding-top: 32px;
            width: 270px;
            @media(max-width: 1360px){
                width: 220px;
            }
            @media(max-width: 1200px){
                float: none;
                padding-bottom: 110px;
                margin-bottom: 40px;
                width: auto;
                display: none;
            }
            .ev-statist-wrap{
                @media(max-width: 1200px){
                    display: inline-block;
                    vertical-align: top;
                }
                .ev-soc-photo{
                    margin-bottom: 17px;

                    position: relative;
                    width: 180px;
                    height: 40px;
                    a{
                        opacity: 1;
                        width: 40px;
                        height: 40px;
                        position: absolute;
                        top: 0;
                        left: 0;
                        &:nth-child(2){
                            left: 25px;
                        }
                        &:nth-child(3){
                            left: 49px;
                        }
                        &:nth-child(4){
                            left: 72px;
                        }
                        &:nth-child(5){
                            left: 91px;
                        }
                        &:nth-child(6){
                            left: 116px;
                        }
                        &:nth-child(7){
                            left: 140px;
                        }
                        img{
                            width: 100%;
                        }
                    }
                }
                .ev-statistics{
                    margin-bottom: 38px;
                    position: relative;
                    img{
                        position: absolute;
                        top: 7px;
                        left: 110px;
                        &.dot-2{
                            top: 36px;
                            left: 70px;
                        }
                    }
                    p{
                        margin-bottom: 10px;
                        color: rgb(42, 42, 42);
                        font-size: 14px;
                        font-weight: 400;
                        font-weight: 400;
                        span{
                            padding-left: 28px;
                        }
                    }
                    .share{
                        position: relative;
                        margin-top: 26px;
                        padding-left: 12px;
                        transition: 0.7s;
                        &:hover{
                            opacity: 0.6;
                        }
                        img{
                            position: absolute;
                            top: -3px;
                            left: 3px;
                        }
                        h2{
                            padding-left: 30px;
                            margin: 0;
                            color: rgb(49, 131, 251);
                            font-size: 14px;
                            font-weight: 600;
                            line-height: 14px;
                            text-transform: uppercase;
                        }
                    }
                }
            }
            .buy-ticket{
                cursor: pointer;
                @media(max-width: 1200px){
                    position: absolute;
                    bottom: 0;
                    left: calc(50% - 100px);
                }
                width: 200px;
                height: 85px;
                display: block;
                text-decoration: none;
                border-radius: 5px;
                background-image: linear-gradient(270deg, rgb(136, 137, 242) 0%, rgb(49, 131, 251) 100%);
                p{
                    color: rgb(255, 255, 255);
                    font-size: 14px;
                    font-weight: 400;
                    line-height: 85px;
                    text-align: center;
                    text-transform: uppercase;
                }
            }
            .left-to-event{
                margin-top: 61px;
                @media(max-width: 1200px){
                    display: inline-block;
                    vertical-align: top;
                    margin-top: 0;
                    margin-left: calc(100% - 392px);
                }
                @media(max-width: 500px){
                    margin-left: 0;
                    display: block;
                }
                h1{
                    color: rgb(42, 42, 42);
                    font-size: 18px;
                    font-weight: 600;
                    margin: 0;
                    margin-bottom: 10px;
                    // line-height: 25px;
                }
                p{
                    vertical-align: top;
                    display: inline-block;
                    color: rgb(42, 42, 42);
                    font-size: 35px;
                    font-weight: 600;
                    margin-right: 10px;
                    // line-height: 40px;
                    span{
                        display: block;
                        color: rgb(148, 148, 148);
                        font-size: 12px;
                        font-weight: 400;
                        // line-height: 12px;
                        text-transform: uppercase;
                    }
                }
            }
        }




        .ev-c-footer{
            margin-bottom: 220px;
            .f-caption{
                margin-bottom: 50px;
                p{
                    text-align: center;
                    color: rgb(42, 42, 42);
                    font-size: 35px;
                    font-weight: 600;
                    // line-height: 40px;
                }
            }
            .f-event{
                cursor: pointer;
                vertical-align: top;
                position: relative;
                display: inline-block;
                margin-right: 30px;
                width: calc(33% - 20px);
                @media(max-width: 650px){
                    display: block;
                    width: 80%;
                    margin: 0 auto 30px;
                }
                &:last-of-type{
                    margin-right: 0;
                    @media(max-width: 650px){
                        margin: 0 auto 30px;
                    }
                }
                img{
                    @media(max-width: 1360px){
                        width: 100%;
                    }
                }
                .button{
                    position: absolute;
                    top: 25px;
                    left: 26px;
                    width: 80px;
                    height: 40px;
                    border-radius: 4px;
                    background-image: linear-gradient(270deg, rgb(136, 137, 242) 0%, rgb(49, 131, 251) 100%);
                    p{
                        opacity: 1;
                        color: rgb(255, 255, 255);
                        font-size: 14px;
                        font-weight: 400;
                        line-height: 40px;
                        text-align: center;
                        text-transform: uppercase;
                        margin: 0;
                    }
                }
                p{
                    opacity: 0.5;
                    color: rgb(42, 42, 42);
                    font-size: 14px;
                    font-weight: 600;
                    // line-height: 14px;
                    text-transform: uppercase;
                    margin-top: 13px;
                    line-height: 14px;
                    margin-bottom: 2px;
                }
                h1{
                    color: rgb(42, 42, 42);
                    font-size: 25px;
                    font-weight: 700;
                    // line-height: 40px;
                    margin: 0;
                    @media(max-width: 1120px){
                        font-size: 22px;
                    }
                }
            }
        }
    }
}