.menu{
    z-index: 25;
    width: 100%;
    height: 100px;
    position: fixed;
    top: 0;
    left: 0;

    // background-image: linear-gradient(270deg, #8889f2 0%, #3183fb 100%);
    
    padding: 29px 50px;
    font-size: 0;
    transition: .5s;
    background: rgba(49, 131, 251, 0);
    &.scroll-down{
        background: rgba(49, 131, 251, 1);

        .span-wrap{
            span{
                opacity: 1;
                box-shadow: inset -500px -100px 201px -100px #8889f2;
                transition: opacity .3s linear;
                animation: grad 6.5s infinite;
                animation-delay: .5s;
            }
        }
    }

    .span-wrap{
        width: 100%;
        height: 100px;
        overflow: hidden;
        position: absolute;
        top: 0;
        left: 0;
        span{
            opacity: 0;
            width: 100%;
            height: 100px;
            position: absolute;
            transform: skew(70deg)scale(1.4);
            right: 0;
            top: 0;
            
            // box-shadow: inset 0 -100px 201px -100px #8889f2;
        }
    }
    .column-menu{
        display: inline-block;
        position: relative;
        z-index: 2;
        .element{
            height: 42px;
            display: inline-block;
            border-right: 1px solid rgba(255, 255, 255, 0.5);
            vertical-align: middle;
            position: relative;
            font-weight: 600;
            line-height: 40px;
            font-size: 14px;
            cursor: pointer;
            &:hover{
                p{
                    opacity: 0.5;
                }
                &.country{
                    .drope-country{
                        p{
                            opacity: 1;
                        }
                    }
                }
                &.account{
                    img{
                        opacity: 1;
                    }
                }
                &.language{
                    .drope-language{
                        p{
                            opacity: 1;
                        }
                    }
                }
                &.burger-wrap{
                    opacity: 0.5;
                }
                &.logo{

                    img{

                        opacity: 1;
                    }
                    p{

                        opacity: 1;
                    }
                }
                img{
                    opacity: 0.5;
                }
            }

            .arrow{
                display: inline-block;
                margin-left: 2px;
                width: 12px;
                img{
                    transition: all.3s, transform .7s;
                    width: 10px;
                    height: 6px;
                }
            }
            .img-cursor{
                img{
                    transition: .3s;
                }
            }
            &:last-child{
                border-right: none;
            }
            img{
                display: inline-block;
            }
            p{
                transition: .3s;
                padding-top: 4px;
                font-size: 14px;
                display: inline-block;
                color: #ffffff;
                font-weight: 600;
                line-height: 40px;
            }
            &.logo{
                width: 159px;
                padding-left: 68px;
                img{
                    position: absolute;
                    top: 1px;
                    left: 0;
                }
                a{
                    position: absolute;
                    top: 0;
                    left: 0;
                    bottom: 0;
                    right: 0;
                    z-index: 2;
                }
                p{
                    padding-top: 0;
                    font-size: 16px;
                }
            }
            &.country{
                padding-left: 80px;
                width: 182px;
                cursor: pointer;
                animation: over 7s forwards;
                &.active{
                    overflow: inherit;
                    p{
                        opacity: 0.5;
                    }
                    .arrow{
                        img{
                            opacity: .5;
                            transform: rotate(180deg);
                        }
                    }
                    .img-cursor{
                        img{
                            opacity: .5;
                        }
                    }
                    .drope-country{
                        p{
                            opacity: 1;
                        }
                    }
                    .drope-country{
                        clip-path: polygon(100% 0, 100% 100%, 0 100%, 0 0);
                        .d-c-w{
                            // transform: translateY(0);
                            .drope-element{
                                opacity: 1;
                                transition: opacity .2s ease-in .2s;
                                p{

                                }
                            }
                        }
                    }
                }
                .img-cursor{
                    img{
                        position: absolute;
                        top: 13px;
                        left: 52px;
                    }
                }
                
                .drope-country{
                    transition: 0.7s;
                    opacity: 1;
                    position: absolute;
                    top: 71px;
                    left: -10px;
                    z-index: 3;
                    width: 261px;
                    overflow: hidden;
                    padding: 10px;
                    padding-top: 0;
                    transition: .7s cubic-bezier(0.705, 0.180, 0.210, 1.030);
                    clip-path: polygon(100% 0, 100% 0, 0 0, 0 0);
                    .d-c-w{
                        background: rgba(255,255,255,1);
                        box-shadow: 0 2px 16px rgba(49, 131, 251, 0.17);
                        .drope-element-bridge{
                            width: 241px;
                            position: absolute;
                            left: 0;
                            top: -29px;
                            height: 29px;
                            background-color: transparent;
                            z-index: 12;
                        }
                        .drope-element{
                            // background-color: #ffffff;
                            transition: opacity .2s ease-in .7s;
                            opacity: 0;
                            overflow: hidden;
                            &:hover{
                                background-color: #e8f1ff;
                                p{
                                    color: #2a2a2a;
                                }
                            }
                            p{
                                transition: color .3s ease;
                                padding-left: 28px;
                                line-height: 50px;
                                color: #949494;
                                font-size: 14px;
                                font-weight: 600;
                            }
                        }
                    }
                }
            }
            &.language{
                padding-left: 52px;
                cursor: pointer;
                overflow: inherit;
                animation: over 7s forwards;
                &.active{
                    overflow: inherit;
                    p{
                        opacity: 0.5;
                    }
                    .arrow{
                        img{
                            opacity: .5;
                            transform: rotate(180deg);
                        }
                    }
                    .drope-language{
                        clip-path: polygon(100% 0, 100% 100%, 0 100%, 0 0);
                        p{
                            opacity: 1;
                        }
                    }
                }
                .drope-language{

                    z-index: 20;
                    opacity: 1;
                    position: absolute;
                    overflow: hidden;
                    top: 71px;
                    left: -10px;
                    padding: 10px;
                    padding-top: 0;
                    width: 124px;
                    transition: .7s cubic-bezier(0.705, 0.180, 0.210, 1.030);
                    clip-path: polygon(100% 0, 100% 0, 0 0, 0 0);
                    .d-r-l-w{
                        background: #fff;
                        box-shadow: 0 2px 16px rgba(49, 131, 251, 0.17);
                        .drope-element-bridge{
                            width: 124px;
                            position: absolute;
                            left: 0;
                            top: -29px;
                            height: 29px;
                            background-color: transparent;
                            z-index: 12;
                        }
                        .drope-element{
                            // background-color: #ffffff;
                            text-align: center;
                            &:hover{
                                background-color: #e8f1ff;
                                p{
                                    color: #2a2a2a;
                                }
                            }
                            p{
                                line-height: 50px;
                                color: #949494;
                                font-size: 14px;
                                font-weight: 500;
                            }
                        }
                    }
                }
            }
            &.search{
                img{
                    transition: .3s;
                }
                &.active{
                    img{
                        opacity: .5;
                    }
                }
                img{
                    position: absolute;
                    top: 11px;
                    right: 50px;
                }
            }
            &.account{
                padding-left: 130px;
                width: 275px;
                &.profile{
                    // display: none;
                }
                &.login{
                    text-align: center;
                    width: 234px;
                    padding: 0;
                    cursor: pointer;
                    display: none;
                    p{
                        transition: .3s;
                        color: #ffffff;
                        font-size: 14px;
                        font-weight: 600;
                        line-height: 14px;
                        text-transform: uppercase;
                    }
                }
                img{
                    position: absolute;
                    top: -9px;
                    left: 50px;
                }
            }
            &.shoping-car{
                position: relative;
                width: 138px;
                img{
                    transition: .3s;
                    transform: rotate(32deg);
                    position: absolute;
                    top: 7px;
                    left: 49px;
                    @media(max-width: 480px){
                        width: 30px;
                    }
                }
            }
            &.burger-wrap{
                width: 74px;
                transition: .3s;
                .burger-wrapper{
                    position: absolute;
                    width: 24px;
                    height: 19px;
                    top: 12px;
                    left: 50px;
                    padding-top: 9px;
                    
                    &::before{
                        transition: 0.7s;
                        content: "";
                        height: 2px;
                        width: 24px;
                        background: #ffffff;
                        position: absolute;
                        left: 0;
                        top: 0px;
                    }
                    &::after{
                        transition: 0.7s;
                        content: "";
                        height: 2px;
                        width: 24px;
                        background: #ffffff;
                        position: absolute;
                        left: 0;
                        bottom: 0;
                    }
                    .burger{
                        position: absolute;
                        height: 2px;
                        width: 13px;
                        background: #ffffff;
                        transition: 0.7s;
                        top: -1px;
                        bottom: 0;
                        left: 0;
                        margin: auto 0;
                    }
                }
                &.active{
                    .burger-wrapper{
                        opacity: .5;
                        transition: .3s;
                        &::after{
                            transform: rotate(-45deg);
                            transform-origin:bottom left;
                            transition: 0.7s;
                            width: 24px;
                        }
                        &::before{
                            transform: rotate(45deg);
                            transform-origin: top left;
                            transition: 0.7s;
                            width: 25px;
                        }
                        .burger{
                            background-color: transparent;
                            transition: 0.7s;

                        }
                    }
                }
            }
        }
        &.right-part-menu{
            float: right;
        }
    }
    .burger-part-menu{
        width: 100%;
        position: absolute;
        top: 100px;
        left: 0;
        text-align: center;
        padding-bottom: 10px;
        transition: .7s cubic-bezier(.705,.18,.21,1.03);
        clip-path: polygon(100% 0,100% 0,0 0,0 0);
        &.open-burg-menu{
         clip-path: polygon(100% 0,100% 100%,0 100%,0 0);
     }
     .w-burg-p{
        box-shadow: 0 2px 16px rgba(49, 131, 251, 0.17);
        padding: 50px 50px 40px;
        background: #fff;
        .wrap-mobile-leng{
            display: none;
            position: absolute;
            left: 0;
            top: 0;
            @media(max-width: 480px){
                display: block;
            }
            .language{
                padding-left: 20px;
                height: 42px;
                position: relative;
                p{
                    display: inline-block;
                    color: #949494;
                    font-size: 16px;
                    line-height: 42px;
                    font-weight: 300;
                }

                .arrow{

                    display: inline-block;
                    margin-left: 2px;
                    img{
                        transition:.7s;
                    }
                }
            }

        }
        

        .menu-item{
            margin: 0 10px;
            font-family: Helvetica;
            font-size: 14px;
            font-weight: 400;
            line-height: 40px;
            text-transform: uppercase;
            display: inline-block;
            a{
                text-decoration: none;
                color: #949494;
                transition: .3s ease-in;
                &:hover{

                    color: #3183fb;
                }
            }
        }
    }
}
.bottom-part-menu{
    transition: .7s cubic-bezier(.705,.18,.21,1.03);
    clip-path: polygon(100% 0,100% 0,0 0,0 0);
    &.open-burg-menu{
        clip-path: polygon(100% 0,100% 100%,0 100%,0 0);
    }
    transition: 0.7s;
    z-index: -20;
    width: 100%;
    position: absolute;
    top: 100px;
    left: 0;
    padding-bottom: 10px;
    .w-b-p{
        box-shadow: 0 2px 16px rgba(49, 131, 251, 0.17);
        background-color: #ffffff;
        .search-event{
        }
        .content-wrap{
            width: 970px;
            margin: 0 auto;
            position: relative;
            @media(max-width: 1100px){
                margin: 0 60px;
                width: auto;
            }
            p{
                color: #2a2a2a;
                font-size: 14px;
                font-weight: 500;
                line-height: 150px;
                text-transform: uppercase;
            }
            input{
                outline: none;
                width: 100%;
                display: block;
                border: none;
                color: #2a2a2a;
                font-size: 14px;
                font-weight: 500;
                line-height: 150px;
                &::placeholder{
                    opacity: 0.4;
                }
                &:focus::placeholder{
                    color: transparent;
                }
            }
            img{
                position: absolute;
                top: calc(50% - 9px);
                right: 0;
            }
        }
    }
}
@media(max-width: 1150px){
    .column-menu{
        .element{
            border: none;
            &.logo{
                width: 133px;
                margin-right: 30px;
            }
            &.country{
                padding-left: 30px;
                width: 83px;
                .img-cursor{
                    img{
                        left: 0;
                    }
                }
            }
            &.language{
                padding-left: 20px;
            }
            &.search {
                img{
                    right: 35px;
                }
            }
            &.account{
                padding-left: 70px;
                width: 175px;
                margin-right: 30px;
                img{
                    left: 0;
                }
            }
            &.shoping-car{
                width: 69px;
                img{
                    left: 0;
                }
            }
            &.burger-wrap{
                width: 34px;
                .burger-wrapper{
                    left: 0;
                }
            }
        }
    }
}
@media(max-width: 870px){
    padding: 29px 20px;
}
@media(max-width: 780px){
    padding: 29px 10px;

    &.scroll-down{
        background: linear-gradient(270deg, #8889f2 0%, #3183fb 100%);
    }
    .span-wrap{
        display: none;
    }
    .column-menu{
        .element{
            &.logo{
                display: none;
            }
            &.account{
                width: 60px;
                padding: 0;
                img{
                    width: 100%;
                    top: 0;
                    bottom: 0;
                    margin: auto;
                }
                p{
                    display: none;
                }
            }
            &.burger-wrap{
                .burger-wrapper{
                    top: 11px;
                }
            }
        }
    }
}
@media(max-width: 480px){
    .column-menu{
        .element{
            &.account{
                width: 35px;
                padding: 0;
            }
            &.language{
                display: none;
            }
        }
    }
}

@media(max-width: 405px){
    .column-menu{
        .element{
            &.account{
                margin-right: 20px;
            }
            &.search{
                img{
                    right: 20px;
                }
            }
            &.shoping-car{
                width: 49px;
            }
        }
    }
}

}

@keyframes grad {
    0%{
        box-shadow: inset -500px -100px 201px -100px #8889f2;
    }
    50%{
        box-shadow: inset -1000px -100px 201px -100px #8889f2;
    }
    100%{
        box-shadow: inset -500px -100px 201px -100px #8889f2;
    }
}


keyframes over{
    0%{
        overflow: inherit;
    }
    100%{
        overflow: hidden;
    }
}