.account-wrap{
    width: calc(848px + 17vw);
    padding: 203px 2px 465px 2px;
    margin: 0 auto;
    @media(max-width: 1100px){
        width: auto;
        margin: 0 30px;
    }
    @media(max-width: 780px){
        margin: 0 10px;
    }
    @media(max-width: 600px){
        padding-bottom: 265px;
    }
    &::after{
        content: "";
        display: table;
        clear: both;
    }
    .left-part{
        display: inline-block;
        width: 471px;
        @media(max-width: 530px){
            width: 87vw;
        }
        h1{
            color: #2a2a2a;
            font-size: 35px;
            font-weight: 600;
            margin: 0;
            line-height: 27px;
            margin-bottom: 82px;
        }
        a{
            display: block;
            background-color: #e8f1ff;
            border-radius: 4px;
            padding: 35px 50px;
            margin-bottom: 3px;
            color: #3183fb;
            font-size: 14px;
            text-transform: uppercase;
            text-decoration: none;
            line-height: 14px;
            font-weight: 600;
            letter-spacing: 0.18px;
            img{
                float: right;
                position: relative;
                top: 2px;
            }
        }
    }
    .right-part{
        display: inline-block;
        width: 370px;
        float: right;
        padding-top: 109px;

        @media(max-width: 980px){
            float: none;
            display: block;
        }
        @media(max-width: 420px){
            width: auto;
        }
        h1{
            color: #2a2a2a;
            font-size: 14px;
            text-transform: uppercase;
            font-weight: 600;
            margin: 0;
            line-height: 13px;
            letter-spacing: 0.4px;
        }
        h2{
            color: #3183fb;
            font-size: 35px;
            font-weight: 600;
            margin: 0;
            letter-spacing: -0.4px;
            line-height: 65px;
            margin-bottom: 5px;
        }
        p{
            color: #2a2a2a;
            font-size: 14px;
            font-weight: 300;
            opacity: 0.4;
            line-height: 24px;
            margin-bottom: 58px;
        }
        a{
            color: #3183fb;
            font-size: 14px;
            font-weight: 600;
            text-transform: uppercase;
            letter-spacing: 0.2px;
        }
        .btn{
            margin-top: 48px;
            width: 200px;
            height: 85px;
            border-radius: 4px;
            background-image: linear-gradient(270deg, #8889f2 0%, #3183fb 100%);
            cursor: pointer;
                display: block;
                text-decoration: none;
            p{
                color: #ffffff;
                font-size: 14px;
                font-weight: 400;
                text-transform: uppercase;
                opacity: 1;
                text-align: center;
                line-height: 85px;
                margin: 0;
            }
        }
    }
}