    .create-ac, .login-to-account{
        width: calc(590px + 30.2vw);
        padding-top: 200px;
        margin: 0 auto;
        @media(max-width: 950px){
            margin: 0;
            margin-left: 20px;
            width: 572px;
        }
        @media(max-width: 780px){
            margin-left: 10px;
        }
        @media(max-width: 650px){
            width: auto;
            margin: 0 10px;
        }
        .button-wrap{
            font-size: 0;
            width: 358px;
            margin-left: 0;
            background-color: #e8f1ff;
            border-radius: 5px;
            margin-bottom: 54px;
            @media(max-width: 420px){
                width: auto;
            }
            .all-button{
                display: inline-block;
                width: 58%;
                padding: 22px 0;
                cursor: pointer;
                &.active{
                    a{
                        color: #3183fb;
                        opacity: 1;
                    }
                }
                a{
                    line-height: 40px;
                    text-align: center;
                    opacity: 0.5;
                    color: #2a2a2a;
                    font-size: 14px;
                    font-weight: 600;
                    display: block;
                    text-decoration: none;
                    text-transform: uppercase;
                    cursor: pointer;
                    border-right: 1px solid #c7ddfe;
                }
                &.registration{
                    a{
                        margin-right: 2px;
                    }
                }
            }
            .log-in{
                width: 42%;
                a{

                    opacity: 0.5;
                    border: none;
                }
            }
        }
        form{
            width: 572px;
            margin-bottom: 243px;
            @media(max-width: 650px){
                width: auto;
            }
            .caption{
                margin-bottom: 30px;
                p{
                    color: #2a2a2a;
                    font-size: 35px;
                    font-weight: 600;
                    line-height: 26px;
                    letter-spacing: 0.08px;
                }
                h3{
                    margin-top: 45px;
                    opacity: 0.4;
                    color: #2a2a2a;
                    font-size: 14px;
                    width: 425px;
                    font-weight: 300;
                    letter-spacing: -0.1px;
                    line-height: 20px;
                    @media(max-width: 470px){
                        width: auto;
                    }
                }
            }
            .inp-wrap{
                position: relative;
                display: inline-block;
                width: 100%;
                &.half-part-wrap{
                    width: calc(50% - 16px);
                    margin-right: 28px;
                    &.last-part{
                        margin-right: 0;
                    }
                }
                p{
                    position: absolute;
                    top: 0;
                    left: 0;
                    line-height: 46px;
                    color: #3183fb;
                    font-weight: 600;
                    z-index: -5;
                    opacity: 0.4;
                    text-transform: uppercase;
                }
                input{
                    width: 100%;
                    border: none;
                    border-bottom: 1px solid rgba(49, 131, 251, 0.4);;
                    line-height: 44px;
                    font-size: 14px;
                    font-weight: 600;
                    margin-bottom: 25px;
                    outline: none;
                    color:#3183fb;
                    background: transparent;
                    @media(max-width: 440px){
                        font-size: 12px;
                    }
                    &.name-email{
                        padding-top: 11px;
                        &+p{
                            top: 11px;
                        }
                    }
                    &:focus+p{
                        color: transparent;
                    }
                    &::placeholder{
                        color: #3183fb;
                        opacity: 0.4;
                        text-transform: uppercase;
                    }
                    &.half-part{
                        padding-top: 11px;
                        &+p{
                            top: 11px;
                        }
                        
                    }
                }
            }
            .social-wrap{
                margin: 29px 0 50px;
                .caption{
                    margin-bottom: 41px;
                    p{
                        color: #2a2a2a;
                        font-size: 35px;
                        font-weight: 600;
                        line-height: 26px;
                    }
                }
                a{
                    position: relative;
                    left: 1px;
                    display: block;
                    width: 205px;
                    height: 65px;
                    border-radius: 4px;
                    background-color: #3b5998;
                    color: #ffffff;
                    font-size: 14px;
                    font-weight: 200;
                    line-height: 65px;
                    text-transform: uppercase;
                    text-align: center;
                    padding-right: 36px;
                    margin-bottom: 15px;
                    text-decoration: none;
                    letter-spacing: 0.4px;
                    transition: .4s ease-out;
                    &:hover{
                        opacity: 0.7;
                    }
                    &:nth-child(3){
                        background-color: #4c75a3;
                    }
                    &:nth-child(4){
                        background-color: #db4a39;
                    }
                    img{
                        position: absolute;
                        top: 25px;
                        right: 30px;
                    }
                }
            }
            input.checkbox{
                position: absolute;
                opacity: 0;
                width: 0;
                &:not(checked)+label{
                    &:before{
                        content: "";
                        width: 17px;
                        height: 17px;
                        border-radius: 3px;
                        border: 1px solid #3183fb;
                        position: absolute;
                        top: 3px;
                        left: -2px;
                        padding: 1px;
                    }
                }
                &:checked+label{
                    &:after{
                        background: #8989f2;
                        content: "";
                        width: 17px;
                        height: 17px;
                        border-radius: 50%;
                        position: absolute;
                        top: 5px;
                        left: 0px;
                    }
                }
            }
            label{
                position: relative;
                display: inline-block;
                padding-left: 32px;
                width: 457px;
                color: #2a2a2a;
                opacity: 0.4;
                font-size: 14px;
                font-weight: 400;
                line-height: 19px;
                margin-bottom: 52px;
                letter-spacing: -0.1px;
                @media(max-width: 510px){
                    width: auto;
                }
            }
            .submit{
                width: 200px;
                height: 85px;
                border-radius: 4px;
                background:#3183fb;
                color: #ffffff;
                font-size: 14px;
                font-weight: 200;
                text-transform: uppercase;
                line-height: 12px;
                letter-spacing: 0.4px;
                border: none;
                outline: none;
                position: relative;
                overflow: hidden;
                &:hover, &:focus{
                    i{
                        &:before{
                            transform: scale3d(1.2, 1.2, 1.2);
                            transition: 1s cubic-bezier(0.18, 0, 0, 0.995);
                        }
                    }
                }
                &:active{
                   i{
                    &:before{
                        transform: scale3d(2, 2, 2);
                    }
                }
            }
            p{
                position: relative;
            }
            i{
                width: 360px;
                height: 360px;
                position: absolute;
                top: -160%;
                left: 5%;
                pointer-events: none;
                z-index: 0;
                &:before{
                    content: '';
                    position: absolute;
                    top: 0;
                    left: 0;
                    right: 0;
                    bottom: 0;
                    border-radius: 50%;
                    background: radial-gradient(ellipse at center, #8889f2 10%, transparent 65%);
                    transition: transform 0.3s ease-in-out;
                }
            }
        }
    }
}
.login-to-account{
    form{
        .caption{
            margin-bottom: 20px;
        }
        .social-wrap{
            .caption{
                position: relative;
                left: -2px;
                p {
                    letter-spacing: -0.05px;
                    span{
                        margin-left: 10px;
                    }
                }
            }
        }
    }
}