
.greatest_showman{
    // 
    padding: 193px 0;
    // 
    font-size: 0;
    width: 1175px;
    margin: 0 auto;
    @media(max-width: 1450px){
        width: 80%;
        margin: 0 50px;
    }
    @media(max-width: 870px){
        margin: 0 20px;
    }
    @media(max-width: 780px){
        margin: 0 10px;
    }
    @media(max-width: 500px){
        width: calc(27px + 72vw);
    }
    &::after{
        content: "";
        display: table;
        clear: both;
    }
    .left-column{
        padding-left: 2px;
        display: inline-block;
        // display: none;

        .caption{
            h1{
                margin: 0;
                margin-bottom: 34px;
                color: #2a2a2a;
                font-size: 35px;
                font-weight: 600;
                // line-height: 40px;
            }
        }
        p{
            color: #2a2a2a;
            font-size: 18px;
            font-weight: 600;
            margin-bottom: 22px;
            // line-height: 25px;
        }
        .price{
            margin-bottom: 67px;
            p{}

        
            .button{
                cursor: pointer;
                display: inline-block;
                margin-right: 35px;
                width: 80px;
                height: 40px;
                border-radius: 4px;
                background-color: #fe0061;
                &:last-of-type{
                    margin: 0;
                }
                &.bottom-price{
                    background-color: #00cd3e;
                }
                &.middle-price{
                    background-color: #ff9300;
                }
                p{
                    color: #ffffff;
                    font-size: 14px;
                    font-weight: 400;
                    line-height: 40px;
                    text-transform: uppercase;
                    text-align: center;
                }
            }
        }
        .scene{
            position: relative;
            font-size: 0;
            width: 440px;
            @media(max-width: 980px){
                width: 386px;
            }
            input{
                width: 0;
                height: 0;
                position: absolute;
            }
            label{
                cursor: pointer;
                display: inline-block;
                margin: 0 4px 3px 0;
                width: 50px;
                height: 50px;
                border-radius: 2px;
                &:nth-of-type(2n){
                    margin-right: 3px;
                }
                &:nth-of-type(8n){
                    margin-right: 0;
                }
                &.bought{
                    border: 1px solid #c7ddfe;
                    background-color: #e8f1ff;
                }
                @media(max-width: 980px){
                    width: 45px;
                    height: 45px;
                }
            }
            
            .red-rows{
                label{
                    border: 1px solid #fe0061;
                    &.bought{
                        border: 1px solid #c7ddfe;
                        background-color: #e8f1ff;
                    }
                }
                input:checked+label{
                    background: #fe0061 url("../img/pics/check.png") no-repeat 50% 50%;
                    &.bought{
                        background: #e8f1ff;
                    }
                }
            }
            .yellow-rows{
                label{
                    border: 1px solid #ff9300;
                    &.bought{
                        border: 1px solid #c7ddfe;
                        background-color: #e8f1ff;
                    }
                }
                input:checked+label{
                    background: #ff9300 url("../img/pics/check.png") no-repeat 50% 50%;
                    &.bought{
                        background:  #e8f1ff;
                    }
                }
            }
            .green-rows{
                label{
                    border: 1px solid #00cd3e;
                    &.bought{
                        border: 1px solid #c7ddfe;
                        background-color: #e8f1ff;
                    }
                }
                input:checked+label{
                    background: #00cd3e url("../img/pics/check.png") no-repeat 50% 50%;
                    &.bought{
                        background: #e8f1ff;
                    }
                }
            }
            
        }
        @media(max-width: 500px){
            .caption{
                h1{
                    font-size: 30px;
                }
            }
            .price{
                .button{
                    margin-right: 15px;
                }
            }
            .scene{
                width: auto;
                .place{
                    width: 9vw;
                    height: 9vw;
                }
            }
        }
    }
// 
// 
// 
    .right-column{
        padding-right: 132px;
        padding-top: 4px;
        float: right;
        @media(max-width: 1250px){
            padding-right: 0;
        }
        @media(max-width: 880px){
            float: none;
            margin-top: 40px;
        }
        .info-bar{
            margin-bottom: 100px;
            p{
                color: #949494;
                font-size: 16px;
                font-weight: 400;
                margin-bottom: 37px;
                &.name{
                    color: #2a2a2a;
                    font-size: 18px;
                    font-weight: 600;
                    width: 240px;
                    line-height: 25px;
                    margin-bottom: 3px;
                }
                &.category{
                    text-transform: uppercase;
                    margin-bottom: 18px;
                    font-size: 14px;
                    font-weight: 600;
                    letter-spacing: 0.1px;
                }
            }
            .time-line{
                position: relative;
                padding-left: 37px;
                p{
                    color: #3183fb;
                    font-size: 14px;
                    font-weight: 600;
                    text-transform: uppercase;
                    margin-bottom: 13px;
                    // line-height: 14px;
                }
                img{
                    position: absolute;
                    top: -5px;
                    left: 0px;
                }
            }
            .time-start{
                position: relative;
                padding-left: 37px;
                p{
                    color: #3183fb;
                    font-size: 14px;
                    font-weight: 600;
                    text-transform: uppercase;
                    // line-height: 14px;
                }
                img{
                    position: absolute;
                    top: 0px;
                    left: 0px;
                }
            }
        }
        .tikets{
            h1{
                color: #2a2a2a;
                font-size: 25px;
                font-weight: 600;
                margin: 0;
                margin-bottom: 15px;
                // line-height: 35px;
            }
            .tiket-column{
                vertical-align: top;
                display: inline-block;
                margin-right: 24px;
                margin-bottom: 11px;
                h2{
                    margin-bottom: 12px;
                    color: #949494;
                    font-size: 12px;
                    font-weight: 400;
                    // line-height: 12px;
                    text-transform: uppercase;
                }
                p{
                    color: #2a2a2a;
                    font-size: 25px;
                    font-weight: 600;
                    margin-bottom: 29px;
                    // line-height: 35px;
                }
                &.cancel-column{
                    margin-right: 0;
                    width: 14px;
                    .fabul{
                        height: 28px;
                    }
                    .cancel{
                        cursor: pointer;
                        width: 14px;
                        height: 63px;
                        background: url("../img/pics/red-crose.png") no-repeat 0 11px;
                        transition: 0.5s;
                        &:hover{
                            opacity: 0.6;
                        }
                    }
                }
            }
            .bottun-next{
                outline: none;
                border: none;
                cursor: pointer;
                margin-top: 33px;
                margin-bottom: 83px;
                width: 200px;
                height: 85px;
                border-radius: 4px;
                background-image: linear-gradient(270deg, #8889f2 0%, #3183fb 100%);
                p{
                    color: #ffffff;
                    font-size: 14px;
                    font-weight: 400;
                    line-height: 85px;
                    text-transform: uppercase;
                    text-align: center;
                }
            }
        }
        .time-left{
            vertical-align: top;
            h1{
                margin: 0;
                margin-bottom: 10px;
                color: #2a2a2a;
                font-size: 18px;
                font-weight: 600;
                // line-height: 25px;
            }
            p{
                display: inline-block;
                color: #2a2a2a;
                font-size: 35px;
                font-weight: 600;
                // line-height: 40px;
            }
            h2{
                color: #949494;
                font-size: 12px;
                font-weight: 400;
                // line-height: 12px;
                text-transform: uppercase;
            }
            .min{
                vertical-align: top;
                display: inline-block;
                margin-right: 10px;
            }
            .second{
                margin-left: 10px;
                vertical-align: top;
                display: inline-block;
            }
        }
    }
}