.thank-you-wrap{
    background: rgba(255, 255, 255, 0.9);
    width: 100%;
    height: 100vh;
    position: fixed;
    z-index: 3;
    padding: 493px 30px 250px 0;
    display: none;
    .thank-you{
        margin: 0 auto;
        width: 800px;
        height: 500px;
        border-radius: 4px;

        background-color: #e8f1ff;
        text-align: center;
        padding-top: 70px;
        @media(max-width: 900px){
            padding: 70px 13vw 0;
            width: 89vw;
        }
        img{
            width: 80px;
            margin: 0 auto;
            margin-bottom: 46px;
            &.event-was-created{
                width: 52px;
            }
        }
        h1{
            margin: 0;
            margin-bottom: 23px;
            line-height: 26px;
            color: #2a2a2a;
            font-size: 35px;
            font-weight: 600;
            }
        p{
            display: inline-block;
            width: 60%;
            margin-left: 40px;
            text-align: left;
            opacity: 0.4;
            // opacity: 1;
            color: #2a2a2a;
            font-size: 14px;
            font-weight: 200;
            line-height: 24px;
            letter-spacing: -0.11px;
        }
        .btn{
            width: 200px;
            height: 85px;
            border-radius: 4px;
            background-image: linear-gradient(270deg, #8889f2 0%, #3183fb 100%);
            margin: 60px calc(50% - 115px) 0 calc(50% - 85px);

            cursor: pointer;
            padding-left: 5px;
            p{
                opacity: 1;
                color: #ffffff;
                font-size: 14px;
                font-weight: 200;
                line-height: 85px;
                text-transform: uppercase;
            }
        }
    }
}