//colors
$color_celeste_approx: #ccc;
$black:  black;
$color_dodger_blue_17_approx: rgba(49,131,251,.17);
$color_alto_approx: #dddddd;
$white: #ffffff;
$color_mine_shaft_approx: #333333;
$color_solitude_approx: #e8f1ff;
$color_tundora_approx: #454545;
$color_baltic_sea_approx: #2b2b2b;
$color_danube_approx: rgb(94, 158, 214);
$color_dodger_blue_approx: #3183fb;
$color_blue_ribbon_approx: #003eff;
$color_tropical_blue_approx: #c7ddfe;
$color_confetti_approx: #dad55e;
$color_witch_haze_approx: #fffa90;
$color_crete_approx: #777620;
$color_mona_lisa_approx: #f1a899;
$color_pippin_approx: #fddfdf;
$color_kabul_approx: #5f3f3f;
$color_silver_chalice_approx: #aaaaaa;
$color_storm_dust_approx: #666666;

//fonts
$font_0: Helvetica;
$font_1: Arial;
$font_2: sans-serif;

//data-uris
$data_uri_0: "data:image/gif;base64,R0lGODlhAQABAIAAAAAAAP///yH5BAEAAAAALAAAAAABAAEAAAIBRAA7";
$data_uri_1: "data:image/gif;base64,";

//urls
$url_0: url(../img/pics/arrow.png);
$url_1: url(images/ui-icons_ffffff_256x240.png);
$url_2: url(images/ui-icons_777620_256x240.png);
$url_3: url(images/ui-icons_cc0000_256x240.png);
$url_4: url(images/ui-icons_777777_256x240.png);

//@extend-elements
//original selectors
//.ui-helper-clearfix:before, .ui-helper-clearfix:after
%extend_1 {
    content: "";
    display: table;
    border-collapse: collapse;
}

//original selectors
//.ui-checkboxradio-radio-label.ui-checkboxradio-checked .ui-icon, .ui-checkboxradio-radio-label.ui-checkboxradio-checked:hover .ui-icon
%extend_2 {
    background-image: none;
    width: 8px;
    height: 8px;
    border-width: 4px;
    border-style: solid;
}

//original selectors
//.ui-datepicker .ui-datepicker-prev, .ui-datepicker .ui-datepicker-next
%extend_3 {
    position: absolute;
    width: 15px;
    height: 16px;
    top: 0;
    bottom: 0;
    margin: auto;
    cursor: pointer;
}

//original selectors
//.ui-datepicker .ui-datepicker-prev span, .ui-datepicker .ui-datepicker-next span
%extend_4 {
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    margin: auto;
}

//original selectors
//.ui-datepicker td span, .ui-datepicker td a
%extend_5 {
    display: block;
    padding: .2em;
    text-align: right;
    text-decoration: none;
}

//original selectors
//.ui-state-default, .ui-widget-content .ui-state-default, .ui-widget-header .ui-state-default, .ui-button, html .ui-button.ui-state-disabled:hover, html .ui-button.ui-state-disabled:active
%extend_6 {
    border: 1px solid $color_solitude_approx;
    background: $color_solitude_approx;
    font-weight: normal;
    color: $color_tundora_approx;
}

//original selectors
//.ui-state-hover, .ui-widget-content .ui-state-hover, .ui-widget-header .ui-state-hover, .ui-state-focus, .ui-widget-content .ui-state-focus, .ui-widget-header .ui-state-focus, .ui-button:hover, .ui-button:focus
%extend_7 {
    opacity: .5;
    font-weight: normal;
    color: $color_baltic_sea_approx;
}

//original selectors
//.ui-state-active, .ui-widget-content .ui-state-active, .ui-widget-header .ui-state-active, a.ui-button:active, .ui-button:active, .ui-button.ui-state-active:hover
%extend_8 {
    border: 1px solid $color_dodger_blue_approx;
    background: $color_dodger_blue_approx;
    font-weight: normal;
    color: $white;
}

//original selectors
//.ui-state-highlight, .ui-widget-content .ui-state-highlight, .ui-widget-header .ui-state-highlight
%extend_9 {
    border: 1px solid $color_tropical_blue_approx;
    background: $white;
    color: $color_mine_shaft_approx;
}

//original selectors
//.ui-state-error, .ui-widget-content .ui-state-error, .ui-widget-header .ui-state-error
%extend_10 {
    border: 1px solid $color_mona_lisa_approx;
    background: $color_pippin_approx;
    color: $color_kabul_approx;
}

//original selectors
//.ui-priority-secondary, .ui-widget-content .ui-priority-secondary, .ui-widget-header .ui-priority-secondary
%extend_11 {
    opacity: .7;
    filter: Alpha(Opacity=70);
    font-weight: normal;
}

//original selectors
//.ui-state-disabled, .ui-widget-content .ui-state-disabled, .ui-widget-header .ui-state-disabled
%extend_12 {
    opacity: .35;
    filter: Alpha(Opacity=35);
    background-image: none;
}


.ui-helper-hidden {
    display: none;
}
.ui-helper-hidden-accessible {
    border: 0;
    clip: rect(0 0 0 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
}
.ui-helper-reset {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    line-height: 1.3;
    text-decoration: none;
    font-size: 100%;
    list-style: none;
}
.ui-helper-clearfix {
    &:before {
        @extend %extend_1;
    }
    &:after {
        @extend %extend_1;
        clear: both;
    }
}
.ui-helper-zfix {
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    position: absolute;
    opacity: 0;
    filter: Alpha(Opacity=0);
}
.ui-front {
    z-index: 100;
}
.ui-state-disabled {
    cursor: default !important;
    pointer-events: none;
    @extend %extend_12;
    .ui-icon {
        filter: Alpha(Opacity=35);
    }
}
.ui-icon {
    display: inline-block;
    vertical-align: middle;
    margin-top: -.25em;
    position: relative;
    text-indent: -99999px;
    overflow: hidden;
    background-repeat: no-repeat;
    width: 10px;
    height: 6px;
    //Instead of the line below you could use @include transform($scale, $rotate, $transx, $transy, $skewx, $skewy, $originx, $originy)
    transform: rotate(270deg);
    //Instead of the line below you could use @include transition($transition-1, $transition-2, $transition-3, $transition-4, $transition-5, $transition-6, $transition-7, $transition-8, $transition-9, $transition-10)
    transition: .3s;
    background-image: $url_0;
}
.ui-widget-icon-block {
    left: 50%;
    margin-left: -8px;
    display: block;
}
.ui-widget-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: $color_silver_chalice_approx;
    opacity: .3;
    filter: Alpha(Opacity=30);
}
.ui-accordion {
    .ui-accordion-header {
        display: block;
        cursor: pointer;
        position: relative;
        margin: 2px 0 0;
        padding: .5em .5em .5em .7em;
        font-size: 100%;
    }
    .ui-accordion-content {
        padding: 1em 2.2em;
        border-top: 0;
        overflow: auto;
    }
}
.ui-autocomplete {
    position: absolute;
    top: 0;
    left: 0;
    cursor: default;
}
.ui-menu {
    list-style: none;
    padding: 0;
    margin: 0;
    display: block;
    outline: 0;
    .ui-menu {
        position: absolute;
    }
    .ui-menu-item {
        margin: 0;
        cursor: pointer;
        list-style-image: url($data_uri_0);
    }
    .ui-menu-item-wrapper {
        position: relative;
        padding: 3px 1em 3px .4em;
    }
    .ui-menu-divider {
        margin: 5px 0;
        height: 0;
        font-size: 0;
        line-height: 0;
        border-width: 1px 0 0 0;
    }
    .ui-state-focus {
        margin: -1px;
    }
    .ui-state-active {
        margin: -1px;
    }
    .ui-icon {
        position: absolute;
        top: 0;
        bottom: 0;
        left: .2em;
        margin: auto 0;
    }
    .ui-menu-icon {
        left: auto;
        right: 0;
    }
}
.ui-menu-icons {
    position: relative;
    .ui-menu-item-wrapper {
        padding-left: 2em;
    }
}
.ui-button {
    padding: .4em 1em;
    display: inline-block;
    position: relative;
    line-height: normal;
    margin-right: .1em;
    cursor: pointer;
    vertical-align: middle;
    text-align: center;
    //Instead of the line below you could use @include user-select($select)
    user-select: none;
    overflow: visible;
    text-decoration: none;
    @extend %extend_6;
    color: $color_tundora_approx;
    text-decoration: none;
    &:link {
        text-decoration: none;
    }
    &:visited {
        text-decoration: none;
    }
    &:hover {
        text-decoration: none;
        @extend %extend_7;
        .ui-icon {
            background-image: $url_0;
            opacity: .5;
        }
    }
    &:active {
        text-decoration: none;
        @extend %extend_8;
        .ui-icon {
            background-image: $url_1;
        }
    }
    &:focus {
        @extend %extend_7;
        .ui-icon {
            background-image: $url_0;
            opacity: .5;
        }
    }
    .ui-icon {
        background-image: $url_4;
    }
    &.ui-icon-notext .ui-icon {
        padding: 0;
        width: 2.1em;
        height: 2.1em;
        text-indent: -9999px;
        white-space: nowrap;
    }
    &.ui-state-active:hover {
        @extend %extend_8;
    }
    .ui-state-highlight.ui-icon {
        background-image: $url_2;
    }
}
.ui-button-icon-only {
    width: 2em;
    //Instead of the line below you could use @include box-sizing($bs)
    box-sizing: border-box;
    text-indent: -9999px;
    white-space: nowrap;
    .ui-icon {
        position: absolute;
        top: 50%;
        left: 50%;
        margin-top: -8px;
        margin-left: -8px;
    }
}
input {
    &.ui-button {
        &.ui-button-icon-only {
            text-indent: 0;
        }
        &.ui-icon-notext .ui-icon {
            width: auto;
            height: auto;
            text-indent: 0;
            white-space: normal;
            padding: .4em 1em;
        }
    }
    &.ui-button::-moz-focus-inner {
        border: 0;
        padding: 0;
    }
}
.ui-controlgroup {
    vertical-align: middle;
    display: inline-block;
    .ui-controlgroup-label {
        padding: .4em 1em;
        span {
            font-size: 80%;
        }
    }
    > .ui-controlgroup-item {
        float: left;
        margin-left: 0;
        margin-right: 0;
        &:focus {
            z-index: 9999;
        }
        &.ui-visual-focus {
            z-index: 9999;
        }
    }
}
.ui-controlgroup-vertical {
    .ui-controlgroup-item {
        //Instead of the line below you could use @include box-sizing($bs)
        box-sizing: border-box;
    }
    .ui-controlgroup-label {
        &.ui-widget-content {
            border-bottom: none;
        }
        + .ui-controlgroup-item {
            border-top: none;
        }
    }
    .ui-spinner-input {
        width: 75%;
        width: calc(100% - 2.4em );
    }
    > .ui-controlgroup-item {
        display: block;
        float: none;
        width: 100%;
        margin-top: 0;
        margin-bottom: 0;
        text-align: left;
    }
    .ui-spinner .ui-spinner-up {
        border-top-style: solid;
    }
}
.ui-checkboxradio-radio-label {
    .ui-icon-background {
        width: 16px;
        height: 16px;
        //Instead of the line below you could use @include border-radius($radius, $vertical-radius)
        border-radius: 1em;
        overflow: visible;
        border: none;
    }
    &.ui-checkboxradio-checked {
        .ui-icon {
            @extend %extend_2;
        }
        &:hover .ui-icon {
            @extend %extend_2;
        }
    }
}
.ui-checkboxradio-disabled {
    pointer-events: none;
}
.ui-datepicker {
    width: 17em;
    padding: .2em .2em 0;
    display: none;
    .ui-datepicker-header {
        position: relative;
        padding: .2em 0;
    }
    .ui-datepicker-prev {
        @extend %extend_3;
        left: 0;
        span {
            @extend %extend_4;
            //Instead of the line below you could use @include transform($scale, $rotate, $transx, $transy, $skewx, $skewy, $originx, $originy)
            transform: rotate(90deg);
        }
    }
    .ui-datepicker-next {
        @extend %extend_3;
        right: 0;
        span {
            @extend %extend_4;
        }
    }
    .ui-datepicker-prev-hover {
        top: 0;
        left: 0;
    }
    .ui-datepicker-next-hover {
        top: 0;
        right: 0;
    }
    .ui-datepicker-title {
        margin: 0 2.3em;
        line-height: 1.8em;
        text-align: center;
        select {
            font-size: 1em;
            margin: 1px 0;
        }
    }
    select {
        &.ui-datepicker-month {
            width: 45%;
        }
        &.ui-datepicker-year {
            width: 45%;
        }
    }
    table {
        width: 100%;
        font-size: .9em;
        border-collapse: collapse;
        margin: 0 0 .4em;
    }
    th {
        padding: .7em .3em;
        text-align: center;
        font-weight: bold;
        border: 0;
    }
    td {
        border: 0;
        padding: 1px;
        span {
            @extend %extend_5;
        }
        a {
            @extend %extend_5;
        }
    }
    .ui-datepicker-buttonpane {
        background-image: none;
        margin: .7em 0 0 0;
        padding: 0 .2em;
        border-left: 0;
        border-right: 0;
        border-bottom: 0;
        button {
            float: right;
            margin: .5em .2em .4em;
            cursor: pointer;
            padding: .2em .6em .3em .6em;
            width: auto;
            overflow: visible;
            &.ui-datepicker-current {
                float: left;
            }
        }
    }
    &.ui-datepicker-multi {
        width: auto;
    }
    .ui-icon {
        display: block;
        text-indent: -99999px;
        overflow: hidden;
        background-repeat: no-repeat;
        left: .5em;
        top: .3em;
    }
}
.ui-datepicker-multi {
    .ui-datepicker-group {
        float: left;
        table {
            width: 95%;
            margin: 0 auto .4em;
        }
    }
    .ui-datepicker-buttonpane {
        clear: left;
    }
    .ui-datepicker-group-last .ui-datepicker-header {
        border-left-width: 0;
    }
    .ui-datepicker-group-middle .ui-datepicker-header {
        border-left-width: 0;
    }
}
.ui-datepicker-row-break {
    clear: both;
    width: 100%;
    font-size: 0;
}
.ui-datepicker-rtl {
    direction: rtl;
    .ui-datepicker-prev {
        right: 2px;
        left: auto;
        &:hover {
            right: 1px;
            left: auto;
        }
    }
    .ui-datepicker-next {
        left: 2px;
        right: auto;
        &:hover {
            left: 1px;
            right: auto;
        }
    }
    .ui-datepicker-buttonpane {
        clear: right;
        button {
            float: left;
            &.ui-datepicker-current {
                float: right;
            }
        }
    }
    .ui-datepicker-group {
        float: right;
    }
    .ui-datepicker-group-last .ui-datepicker-header {
        border-right-width: 0;
        border-left-width: 1px;
    }
    .ui-datepicker-group-middle .ui-datepicker-header {
        border-right-width: 0;
        border-left-width: 1px;
    }
}
.ui-dialog {
    position: absolute;
    top: 0;
    left: 0;
    padding: .2em;
    outline: 0;
    .ui-dialog-titlebar {
        padding: .4em 1em;
        position: relative;
    }
    .ui-dialog-title {
        float: left;
        margin: .1em 0;
        white-space: nowrap;
        width: 90%;
        overflow: hidden;
        text-overflow: ellipsis;
    }
    .ui-dialog-titlebar-close {
        position: absolute;
        right: .3em;
        top: 50%;
        width: 20px;
        margin: -10px 0 0;
        padding: 1px;
        height: 20px;
    }
    .ui-dialog-content {
        position: relative;
        border: 0;
        padding: .5em 1em;
        background: none;
        overflow: auto;
    }
    .ui-dialog-buttonpane {
        text-align: left;
        border-width: 1px 0 0 0;
        background-image: none;
        margin-top: .5em;
        padding: .3em 1em .5em .4em;
        .ui-dialog-buttonset {
            float: right;
        }
        button {
            margin: .5em .4em .5em 0;
            cursor: pointer;
        }
    }
    .ui-resizable-n {
        height: 2px;
        top: 0;
    }
    .ui-resizable-e {
        width: 2px;
        right: 0;
    }
    .ui-resizable-s {
        height: 2px;
        bottom: 0;
    }
    .ui-resizable-w {
        width: 2px;
        left: 0;
    }
    .ui-resizable-se {
        width: 7px;
        height: 7px;
        right: 0;
        bottom: 0;
    }
    .ui-resizable-sw {
        width: 7px;
        height: 7px;
        left: 0;
        bottom: 0;
    }
    .ui-resizable-ne {
        width: 7px;
        height: 7px;
        right: 0;
        top: 0;
    }
    .ui-resizable-nw {
        width: 7px;
        height: 7px;
        left: 0;
        top: 0;
    }
}
.ui-draggable-handle {
    touch-action: none;
}
.ui-resizable {
    position: relative;
}
.ui-resizable-handle {
    position: absolute;
    font-size: 0.1px;
    display: block;
    touch-action: none;
}
.ui-resizable-n {
    cursor: n-resize;
    height: 7px;
    width: 100%;
    top: -5px;
    left: 0;
}
.ui-resizable-s {
    cursor: s-resize;
    height: 7px;
    width: 100%;
    bottom: -5px;
    left: 0;
}
.ui-resizable-e {
    cursor: e-resize;
    width: 7px;
    right: -5px;
    top: 0;
    height: 100%;
}
.ui-resizable-w {
    cursor: w-resize;
    width: 7px;
    left: -5px;
    top: 0;
    height: 100%;
}
.ui-resizable-se {
    cursor: se-resize;
    width: 12px;
    height: 12px;
    right: 1px;
    bottom: 1px;
}
.ui-resizable-sw {
    cursor: sw-resize;
    width: 9px;
    height: 9px;
    left: -5px;
    bottom: -5px;
}
.ui-resizable-nw {
    cursor: nw-resize;
    width: 9px;
    height: 9px;
    left: -5px;
    top: -5px;
}
.ui-resizable-ne {
    cursor: ne-resize;
    width: 9px;
    height: 9px;
    right: -5px;
    top: -5px;
}
.ui-progressbar {
    height: 2em;
    text-align: left;
    overflow: hidden;
    .ui-progressbar-value {
        margin: -1px;
        height: 100%;
    }
    .ui-progressbar-overlay {
        background: url($data_uri_1);
        height: 100%;
        filter: alpha(opacity=25);
        opacity: 0.25;
    }
}
.ui-selectable {
    touch-action: none;
}
.ui-selectable-helper {
    position: absolute;
    z-index: 100;
    border: 1px dotted $black;
}
.ui-selectmenu-menu {
    padding: 0;
    margin: 0;
    position: absolute;
    top: 0;
    left: 0;
    display: none;
    .ui-menu {
        overflow: auto;
        overflow-x: hidden;
        padding-bottom: 1px;
        .ui-selectmenu-optgroup {
            font-size: 1em;
            font-weight: bold;
            line-height: 1.5;
            padding: 2px 0.4em;
            margin: 0.5em 0 0 0;
            height: auto;
            border: 0;
        }
    }
}
.ui-selectmenu-open {
    display: block;
}
.ui-selectmenu-text {
    display: block;
    margin-right: 20px;
    overflow: hidden;
    text-overflow: ellipsis;
}
.ui-slider {
    position: relative;
    text-align: left;
    .ui-slider-handle {
        position: absolute;
        z-index: 2;
        width: 1.2em;
        height: 1.2em;
        cursor: default;
        touch-action: none;
    }
    .ui-slider-range {
        position: absolute;
        z-index: 1;
        font-size: .7em;
        display: block;
        border: 0;
        background-position: 0 0;
    }
    &.ui-state-disabled {
        .ui-slider-handle {
            filter: inherit;
        }
        .ui-slider-range {
            filter: inherit;
        }
    }
}
.ui-slider-horizontal {
    height: .8em;
    .ui-slider-handle {
        top: -.3em;
        margin-left: -.6em;
    }
    .ui-slider-range {
        top: 0;
        height: 100%;
    }
    .ui-slider-range-min {
        left: 0;
    }
    .ui-slider-range-max {
        right: 0;
    }
}
.ui-slider-vertical {
    width: .8em;
    height: 100px;
    .ui-slider-handle {
        left: -.3em;
        margin-left: 0;
        margin-bottom: -.6em;
    }
    .ui-slider-range {
        left: 0;
        width: 100%;
    }
    .ui-slider-range-min {
        bottom: 0;
    }
    .ui-slider-range-max {
        top: 0;
    }
}
.ui-sortable-handle {
    touch-action: none;
}
.ui-spinner {
    position: relative;
    display: inline-block;
    overflow: hidden;
    padding: 0;
    vertical-align: middle;
    a.ui-spinner-button {
        border-top-style: none;
        border-bottom-style: none;
        border-right-style: none;
    }
}
.ui-spinner-input {
    border: none;
    background: none;
    color: inherit;
    padding: .222em 0;
    margin: .2em 0;
    vertical-align: middle;
    margin-left: .4em;
    margin-right: 2em;
}
.ui-spinner-button {
    width: 1.6em;
    height: 50%;
    font-size: .5em;
    padding: 0;
    margin: 0;
    text-align: center;
    position: absolute;
    cursor: default;
    display: block;
    overflow: hidden;
