.about-showapp-wrap{
    font-size: 0;
    color: #2a2a2a;
    font-family: Helvetica;
    padding: 119px 0 248px 0;
    width: 789px;
    margin: 0 auto;
    @media(max-width: 830px){
        width: 94vw;
    }
    @media(max-width: 400px){
        width: 100%;
        padding: 119px 10px 248px;
    }
    h1{
        text-align: center;
        font-size: 35px;
        font-weight: 600;
        margin-bottom: 30px;
        padding-left: 0;
    }
    p{
        padding: 0 5.3vw 0 5.7vw;
        opacity: 0.4;
        font-size: 16px;
        // line-height: 23px;
        font-weight: 300;
        line-height: 1.65;
        @media(max-width: 400px){
            padding: 0;
        }
        &.part{
            margin-top: 26px;
        }
    }
    .social-item-wrap{
        text-align: center;
        .social-wrap{
            display: inline-block;
            margin-right: 31px;
            margin-top: 165px;
            margin-left: 10px;
            &:last-child{
                margin-left: 0;
                margin-right: 0;
            }
            @media(max-width: 830px){
                margin-top: 50px;
            }
            @media(max-width: 400px){
                margin: 50px 0;
            }
            .caption{
                text-align: left;
                p{
                    margin-bottom: 14px;
                    padding: 0;
                    opacity: 1;
                    font-size: 18px;
                    font-weight: 600;
                }
            }
            .content{
                position: relative;
                img.back{
                    transition: 0.7s;
                    opacity: 0.3;
                    @media(max-width: 430px){
                        width: 100%;
                    }
                }
                a{
                    position: absolute;
                    bottom: 30px;
                    left: 30px;
                    @media(max-width: 420px){
                        bottom: 34px;
                    }
                    img{
                        @media(max-width: 420px){
                            width: 80%;
                        }
                    }
                    &:nth-of-type(2){
                        left: 63px;
                        @media(max-width: 420px){
                            left: 56px;
                        }
                    }
                    &:nth-of-type(3){
                        left: 96px;
                        @media(max-width: 420px){
                            left: 82px;
                        }
                    }
                    &:nth-of-type(4){
                        left: 129px;
                        @media(max-width: 420px){
                            left: 108px;
                        }
                    }
                    img{

                    }
                }
                .logo{
                    position: absolute;
                    top: 30px;
                    left: 30px;
                }
                .likes{
                    position: absolute;
                    top: 49px;
                    left: 139px;
                    p{
                        transition: 0.7s;
                        opacity: 1;
                        font-size: 18px;
                        margin: 0;
                        padding: 0;
                        line-height: 25px;
                        &.capt{
                            font-weight: 600;
                            margin-bottom: 2px;
                        }
                    }
                }
                .btn{
                    cursor: pointer;
                    transition: 0.7s;
                    position: absolute;
                    bottom: 30px;
                    right: 30px;
                    padding: 11px 17px 13px 0;
                    border: 1px solid #2a2a2a;
                    border-radius: 3px;
                    .img-wrapper{
                        vertical-align: top;
                        display: inline-block;
                        padding: 5px 22px 5px 16px;
                        width: 45px;
                        height: 24px;
                        border-right: 1px solid #2a2a2a;
                    }
                    p {
                        transition: 0.7s;
                        line-height: 24px;
                        display: inline-block;
                        opacity: 1;
                        font-size: 14px;
                        font-weight: 600;
                        margin-left: 17px;
                        margin-bottom: 0;
                        padding: 0;
                    }
                }
                &.inst{
                    a{
                        img{
                            @media(max-width: 420px){
                                width: 70%;
                            }
                        }
                        left: 30px;
                        &:nth-of-type(2){
                            left: 100px;
                            @media(max-width: 420px){
                                left: 88px;
                            }
                        }
                        &:nth-of-type(3){
                            left: 170px;
                            @media(max-width: 420px){
                                left: 146px;
                            }
                        }
                    }
                    .btn{

                    }
                }
                &:hover{
                    img.back{
                        transition: 0.7s;
                        opacity: 1;
                    }
                    .likes{
                        p{
                            transition: 0.7s;
                            color: #ffffff;
                        }
                    }
                    .btn{
                        transition: 0.7s;
                        border-color: #ffffff;
                        background-color: #ffffff;
                    }
                }
            }
        }
    }
}