.first-screen-wrapper{
    display: inline-block;
    width: 100%;
    position: relative;
    height: 100vh;
    overflow: hidden;
    z-index: 6;
    .drag-wrap{
        width: 298px;
        position: absolute;
        bottom: 78px;
        right: 98px;
        font-size: 0;
        z-index: 20;
        @media(max-width: 1080px){
            // bottom: 137px;
        }
        p{
            vertical-align: top;
            font-size: 14px;
            color: #ffffff;
            opacity: 0.5;
            font-weight: 200;
            line-height: 82px;
            text-transform: uppercase;
            display: inline-block;
            margin-right: 9px;
            transition: .2s;
        }
        .scale{
            display: inline-block;
            width: 247px;
            height: 82px;
            position: relative;
            .line{
                position: absolute;
                top: 0;
                right: 80px;
                bottom: 0;
                margin: auto;
                width: 127px;
                height: 1px;
                background: rgba(255, 255, 255, 0.2);
                transition: .2s;
            }
            .the-slider{
                position: absolute;
                top: 0;
                left: 0;
                bottom: 0;
                margin: auto;
                width: 42px;
                height: 42px;
                border: 2px solid #ffffff;
                border-radius: 50%;
                transition: .2s;
                cursor: grab;
                &.go{
                    width: 80px;
                    height: 80px;
                    transform: translateX(166px);
                }
                &.touch{
                    // cursor: grabbing;
                    transform: scale(0.7);
                    
                    &+.line{
                        width: 134px
                    }
                }
            }
            .arrow-btn{
                position: absolute;
                top: 0;
                right: 0;
                bottom: 0;
                margin: auto;
                display: inline-block;
                width: 80px;
                height: 80px;
                border: 1px solid hsla(0,0%,100%,.2);
                border-radius: 50%;
                text-align: center;
                img{
                    margin-top: 33px;
                    display: inline-block;
                }
            }
        }
    }
    .f-w{
        height: 100vh;
        visibility: hidden;
        position: absolute;
        z-index: 0;
        top: 0;
        left: 0;
        width: 100vw;
        font-size: 0;
        &.animate{
            .w-i{
                &.copy{
                    .clip-wrap{

                        visibility: visible;

                        width: 100%;
                    }
                }

            }
        }
        &.current{
            visibility: visible;
            z-index: 0;
            top: 0;
            left: 0;
            width: 100%;
            .orig{
                visibility: visible;
            }
            
            &.next-slide{
                z-index: 2;
            }
            &.prew-slide{
                z-index: 1;
            }
        }
        &.next-slide{
            z-index: 2;
        }
        &.prew-slide{
            z-index: 1;
        }
        
        .w-i{
            width: 100vw;
            display: inline-block;
            height: 100%;
            position: absolute;
            visibility: hidden;
            overflow: hidden;
            .clip-wrap{
                position: absolute;
                left: 0;
                top: 0;
                right: 0;
            }
            &.orig{
                z-index: -1;
            }
            &.copy{

                width: 25vw;
                position: static;

                &.copy-1{
                    .clip-wrap{
                        .content-wrap{
                            transform: translateX(0);
                        }
                    }
                }
                &.copy-2{
                    .clip-wrap{
                        .content-wrap{
                            transform: translateX(-25%);
                        }
                    }
                }
                &.copy-3{
                    .clip-wrap{
                        .content-wrap{
                            transform: translateX(-50%);
                        }
                    }
                }
                &.copy-4{
                    .clip-wrap{
                        .content-wrap{
                            transform: translateX(-75%);
                        }
                    }
                }
                .clip-wrap{
                    position:relative;
                    width: 0;
                    height: 100%;
                    overflow: hidden;
                    transition: 1.3s cubic-bezier(.75,0,.25,1);
                    // width: 100%;
                    .content-wrap{
                        position: absolute;
                        width: 100vw;
                        // transform: scale(1.2);
                        height: 100%;
                    }
                }
            }
        }
    }
    .first-screen{
        width: 100vw;
        position: relative;
        .first-screen-back{
            font-size: 0;
            position: relative;
            overflow: hidden;
            width: 100%;
            height: 100vh;
            .img-wrap{
                width:  100%;
                height:  100vh;
                position:  absolute;
                perspective: 100vw;
                img{
                    transform:translateX(0) translateY(0) translateZ(-15vw) scale(1.4);
                        display: block;
    width: 100%;
    height: 100%;
    overflow: hidden;
    -o-object-fit: cover;
    object-fit: cover;
    -o-object-position: center;
    object-position: center;
    font-family: "object-fit: cover; object-position: center";

                }
            }
            
        }
        .first-screen-footer{
            position: absolute;
            bottom: 79px;
            left: 100px;
            z-index: 5;
            @media(max-width: 680px){
                width: 100%;
                text-align: center;
                bottom: 130px;
                left: 10px;
                .age{
                    display: inline-block;

                }
                h1{
                    text-align: center;
                    font-size: 44px;
                }
                .buton{
                    margin: 0 auto;
                }
            }
            .age{
                color: #ffffff;
                font-size: 35px;
                width: 86px;
                padding-bottom: 8px;
                border-bottom: 1px solid #ffffff;
                font-weight: 600;
                margin-bottom: 30px;
                margin-left: -1px;
            }
            h1{
                color: #ffffff;
                margin: 0;
                font-size: 54px;
                margin-bottom: 18px;
                line-height: 1;
                font-weight: 600;
                letter-spacing: .5px;
            }
            .buton{
                width: 200px;
                height: 85px;
                border-radius: 4px;
                background: #3183fb;
                cursor: pointer;
                position: relative;
                overflow: hidden;
                z-index: 5;
                text-align: center;
                i{
                    width: 360px;
                    height: 360px;
                    position: absolute;
                    top: -160%;
                    left: 5%;
                    pointer-events: none;
                    z-index: 0;
                    &:before{
                        content: '';
                        position: absolute;
                        top: 0;
                        left: 0;
                        right: 0;
                        bottom: 0;
                        border-radius: 50%;
                        background: radial-gradient(ellipse at center, #8889f2 10%, transparent 65%);
                        transition: transform 0.3s ease-in-out;
                    }
                }
                a{
                    color: #fff;
                    font-family: Helvetica;
                    font-size: 13px;
                    font-weight: 200;
                    text-transform: uppercase;
                    text-align: center; 
                    line-height: 83px;
                    position: relative;
                    letter-spacing: 1.1px;
                    text-decoration: none;

                }
                &:hover, &:focus{
                    i{
                        &:before{
                            transform: scale3d(1.2, 1.2, 1.2);
                            transition: 1s cubic-bezier(0.18, 0, 0, 0.995);
                        }
                    }
                }
                &:active{
                 i{
                    &:before{
                        transform: scale3d(2, 2, 2);
                    }
                }
            }
        }
        
    }
}
@media(max-width: 680px){
    .drag-wrap{
        bottom: 30px;
        margin: 0 auto;
        left: 0;
        right: 0;
    }
    .first-screen{
        .first-screen-footer{
            width: 100%;
            text-align: center;
            bottom: 130px;
            left: 10px;
            .age{
                display: inline-block;
            }
            h1{
                text-align: center;
                font-size: 44px;
            }
            .buton{
                margin: 0 auto;
            }
        }
    }
}
}

.efect-wrap{
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    z-index: 3;
    background-image: linear-gradient(180deg, #000000 0%, rgba(0, 0, 0, 0) 24%, rgba(0, 0, 0, 0) 79%, #000000 100%);
}
.overlay{
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    background-color: #2a2a2a;
    opacity: 0.2;
    z-index: 4;
}

