// Fonts
// =========
@include font-face('Helvetica', Helvetica, light, HelveticaLight, 200);
@include font-face('Helvetica', Helvetica, regular, HelveticaRegular, 300);
@include font-face('Helvetica', Helvetica, bold, HelveticaBold, 600);

body {
  @include font-family('Helvetica',200,normal,sans-serif);
}

// Base typography
// ===================
html {
    font-size: $base-font-size;
    line-height: $base-line-height;
}
