.phone-wrap{
    color: #2a2a2a;
    font-family: Helvetica;
    background-color: #e8f1ff;
    padding: 123px 0 13.3vw 0;
    @media(max-width: 1100px){
        overflow: hidden;
    }
    .caption-wrap{
        max-width: calc(672px + 28.95vw);
        margin: 0 auto;
        position: relative;
        padding-left: 527px;
        @media(max-width: 1100px){
            padding-left: 0;
            text-align: center;
        }
        h1{
            margin: 0;
            font-size: 55px;
            font-weight: 600;
            line-height: 60px;
            z-index: 3;
            position: relative;
            @media(max-width: 1100px){
                padding: 0 30px;
            }
            @media(max-width: 740px){
                font-size: 48px;
                line-height: 50px;
                padding: 0;
                margin-bottom: 10px;
            }
            @media(max-width: 650px){
                font-size: 37px;
                line-height: 40px;
            }
            @media(max-width: 540px){
                font-size: 32px;
                line-height: 35px;
            }
            @media(max-width: 540px){
                font-size: 27px;
                line-height: 30px;
            }
        }
        p{
            font-size: 18px;
            font-weight: 400;
            line-height: 40px;
            margin-bottom: 28px;
            z-index: 3;
            position: relative;
            letter-spacing: 0;
            padding-left: 2px;
            @media(max-width: 740px){
                font-size: 16px;
                padding: 0;
                line-height: 20px;
            }
        }
        .btn{
            width: 425px;
            height: 85px;
            border-radius: 4px;
            background-color: #2a2a2a;
            padding: 25px 0;
            color: #ffffff;
            font-size: 0;
            z-index: 3;
            position: relative;
            display: inline-block;
            margin-left: 2px;
            .half-of-button{
                cursor: pointer;
                display: inline-block;
                width: calc(50% + 1px);
                padding-left: 69px;
                position: relative;
                border-right: 1px solid rgba(255, 255, 255, 0.2);
                padding-right: 30px;
                img{
                    position: absolute;
                    top: -2px;
                    left: 25px;
                }
                p{
                    padding: 0;
                    font-size: 14px;
                    font-weight: 200;
                    margin-bottom: 0;
                    letter-spacing: -.1px;
                    line-height: 1.3;
                }
                h1{
                    font-size: 19px;
                    font-weight: 600;
                    line-height: 23px;
                    padding: 0;
                    line-height: 18px;
                    letter-spacing: .4px;
                }
                &.right-part{
                    border-right: none;
                    padding-right: 0;
                    width: calc(50% - 1px);
                    h1{    padding-left: 2px;

                    }
                    p{
                        letter-spacing: .4px;
                        padding-left: 1px;
                    }
                }
            }
        }
    }
    
    .iphon{
        position: absolute;
        top: -233px;
        left: 133px;
        display: inline-block;
        @media(max-width: 1100px){
            position: relative;
            top: 0;
            left: 0;
            margin-bottom: 40px;
        }
        img.circle{
            position: absolute;
            top: 10px;
            left: -150px;
            @media(max-width: 680px){
                width: 200%;
                left: -115px;
            }
        }
        .slider-border{
            position: relative;
            z-index: 2;
            width: 309px;
            height: 650px;
            overflow: hidden;
            background: url(../img/pics/iphoneX1.svg);
            @media(max-width: 680px){
                width: 257px;
                height: 540px;
            }
        }
        .slider{
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            left: 0;
            padding: 7px;
            border-radius: 55px;
            overflow: hidden;
            @media(max-width: 680px){
                border-radius: 50px;
            }
            .slick-slide{
                display: inline-block;
                width: 100%;
            }
            .slider-item{
                width: 100%;
                height: 100%;
                
            }
        }
    }
    
}


.slick-list{
    overflow: hidden;
}


@media(max-width: 480px){
    .phone-wrap{
        .caption-wrap{
            padding: 0 10px;
            .btn{
                max-width: 330px;
                width: 100%;
                height: 61px;
                margin: 0;
                padding: 15px 0;
                .half-of-button{
                    padding-left: 39px;
                    padding-right: 10px;
                    img{
                        height: 29px;
                        left: 15px;
                        top: 0;
                    }
                    p{
                        line-height: 1.1;
                        padding-left: 13px;
                        font-size: 13px;
                    }
                    h1{
                        padding-left: 13px;
                        font-size: 15px;
                        line-height: 15px;
                    }
                }
            }
        }
    }
}