.pers-inf{
    width: calc(607px + 30.2vw);
    padding-top: 193px;
    margin: 0 auto;
    @media(max-width: 950px){
        margin: 0;
        margin-left: 40px;
        width: 428px;
    }
    @media(max-width: 780px){
        margin: 0 10px;
    }
    @media(max-width: 460px){
        width: auto;
    }
    @media(max-width: 420px){
        font-size: 10px;
}


    .pers-inform{
        padding-left: 57px;
        position: relative;
        margin-bottom: 50px;
        a{
            content: "";
            position: absolute;
            top: 14px;
            left: 16px;
            width: 11px;
            height: 19px;
            background: url("../img/pics/arrow-left.png") no-repeat 0 0;
        }
        p{
            color: #2a2a2a;
            font-size: 35px;
            font-weight: 600;
                @media(max-width: 500px){
                    font-size: 22px;
                    line-height: 46px;
                }
        }
    }
    .avatar{
        position: relative;
        margin-left: 8px;
        margin-bottom: 39px;
        overflow: hidden;
        width: 180px;
        height: 180px;
        border-radius: 50%;
        h1{
            font-size: 35px;
            line-height: 180px;
            width: 100%;
            text-transform: uppercase;
            color: rgb(42, 42, 42);
            font-weight: 600;
            text-align: center;
            margin: 0;
            position: absolute;
            top: 0;
            left: 0;
        }
        p{
            cursor: pointer;
            width: 180px;
            position: absolute;
            bottom: 0;
            line-height: 50px;
            text-align: center;
            background-color: rgba(49, 131, 251, 0.08);
            color: #ffffff;
            font-size: 14px;
            font-weight: 600;
            // line-height: 14px;
            text-transform: uppercase;
        }
        .back{
            width: 180px;
            height: 180px;
            background: rgb(232, 241, 255);
            border-radius: 50%;
        }
    }
    form{
        width: 420px;
        margin-bottom: 243px;
        margin-left: 8px;
        @media(max-width: 950px){
        width: auto;
        }
        .caption{
            p{
                color: #2a2a2a;
                font-size: 14px;
                font-weight: 600;
                line-height: 26px;
                letter-spacing: 0.08px;
            }
            &.change-pas{
                margin-top: 44px;
                margin-bottom: 18px;
            }
        }
        .label{
            width: 100%;
            position: relative;
            display: block;
            margin-bottom: 26px;
            p{
                position: absolute;
                top: 0;
                left: 0;
                line-height: 46px;
                color: #3183fb;
                font-weight: 600;
                z-index: -5;
                text-transform: uppercase;
                opacity: 0.4;
            }
            a{
                cursor: pointer;
                position: absolute;
                width: 14px;
                height: 14px;
                top: 16px;
                right: 5px;
                background: url("../img/pics/red-crose.png");
                transition: 0.5s;
                &:hover{
                    opacity: 0.6;
                }
            }
        
            input{
                height: 46px;
                margin-left: 1px;
                width: calc(100% - 50px);
                border: none;
                border-bottom: 1px solid rgba(49, 131, 251, 1);
                line-height: 42px;
                font-size: 14px;
                font-weight: 600;
                outline: none;
                padding-bottom: 3px;
                color: #3183fb;
                background: transparent;
                @media(max-width: 440px){
                    font-size: 12px;
                }
                &:focus+p{
                    color: transparent;
                }
                &::placeholder{
                    color: #3183fb;
                    opacity: 1;
                    text-transform: uppercase;
                }
            }
        }
        .submit{
            margin-top: 23px;
            width: 200px;
            height: 85px;
            border-radius: 4px;
            background:#3183fb;
            color: #ffffff;
            font-size: 14px;
            font-weight: 200;
            text-transform: uppercase;
            line-height: 12px;
            letter-spacing: 0.4px;
            padding-left: 1px;
            padding-top: 2px;
            position: relative;
            outline: none;
            border: none;
            overflow: hidden;
            &:hover, &:focus{
                    i{
                        &:before{
                            transform: scale3d(1.2, 1.2, 1.2);
                            transition: 1s cubic-bezier(0.18, 0, 0, 0.995);
                        }
                    }
                }
                &:active{
                 i{
                    &:before{
                        transform: scale3d(2, 2, 2);
                    }
                }
            }
            i{
                    width: 360px;
                    height: 360px;
                    position: absolute;
                    top: -160%;
                    left: 5%;
                    pointer-events: none;
                    z-index: 0;
                    &:before{
                        content: '';
                        position: absolute;
                        top: 0;
                        left: 0;
                        right: 0;
                        bottom: 0;
                        border-radius: 50%;
                        background: radial-gradient(ellipse at center, #8889f2 10%, transparent 65%);
                        transition: transform 0.3s ease-in-out;
                    }
                }
                p{
                    position: relative;
                }
        }
    }
}