
.create-event{
    padding-top: 176px;
    width: calc(590px + 30.2vw);
    margin: 0 auto;
    @media(max-width: 980px){
        margin: 0;
        margin-left: 40px;
        width: 571px;
    }
    @media(max-width: 870px){
        margin-left: 20px;
    }

    @media(max-width: 780px){
        margin: 0 10px;
    }
    @media(max-width: 590px){
        width: calc(100% - 20px);
    }
    h1{
        color: #2a2a2a;
        font-size: 35px;
        font-weight: 600;
        line-height: 34px;
        margin-bottom: 35px;
        letter-spacing: 0.05px;
    }
    p{
        opacity: 0.4;
        color: #2a2a2a;
        font-size: 14px;
        font-weight: 400;
        line-height: 24px;
        width: 473px;
        margin-bottom: 36px;
        @media(max-width: 560px){
            width: auto;
        }
    }
    form{
        font-size: 0;
        width: 571px;
        margin-bottom: 254px;
        @media(max-width: 650px){
            width: auto;
        }
        p{
            opacity: 1;
            color: #2a2a2a;
            font-size: 14px;
            font-weight: 600;
            line-height: 12px;
            text-transform: uppercase;
            margin-bottom: 4px;
            &.entry{
                margin-top: 18px;
                margin-bottom: 26px;
            }
            &.cover{
                margin-top: 68px;
                margin-bottom: 21px;
            }
        }
        .label{
            width: 100%;
            position: relative;
            display: block;
            margin-bottom: 26px;
            p{
                position: absolute;
                top: 0;
                left: 0;
                line-height: 46px;
                color: #3183fb;
                font-weight: 600;
                z-index: -5;
                text-transform: uppercase;
                opacity: 0.4;
            }
            &.half-part{
                display: inline-block;
                width: calc(50% - 14px);
                &:nth-of-type(2n){
                    margin-right: 28px;
                }
            }
        }
        input{
            width: 100%;
            border: none;
            outline: none;
            border-bottom: 1px solid rgba(49, 131, 251, 0.4);
            line-height: 46px;
            margin-bottom: 23px;
            font-size: 14px;
            color: #3183fb;
            font-weight: 600;
            background: transparent;
            @media(max-width: 450px){
                font-size: 12px;
            }
            &:focus+p{
                color: transparent;
            }
            &::placeholder{
                color: #3183fb;
                text-transform: uppercase;
                opacity: 0.4;
            }
        }
        input.radio{
            width: 0;
            position: absolute;
            opacity: 0;
            z-index: -10;

            &:not(checked)+label{
                &:before{
                    box-sizing: border-box;
                    content: "";
                    border-radius: 50%;
                    width: 20px;
                    height: 20px;
                    border: 1px solid #3183fb;
                    position: absolute;
                    top: -4px;
                    left: 0px;
                    padding: 1px;
                }
            }
            &:checked+label{
                p{
                    opacity: 1;
                }
                &:after{
                    box-sizing: border-box;
                    background: #8989f2;
                    content: "";
                    border-radius: 50%;
                    width: 10px;
                    height: 10px;
                    position: absolute;
                    top: 1px;
                    left: 5px;
                }
            }
        }
        input.radio+label{
            cursor: pointer;
            display: block;
            position: relative;
            padding-left: 32px;
            margin-bottom: 29px;
            p{
                color: #3183fb;
                opacity: 0.4;
                font-size: 14px;
                font-weight: 600;
                line-height: 10px;
                text-transform: uppercase;
            }
        }
        .description-wrap{
            position: relative;
            .description{
                width: calc(100% - 2px);
                outline: none;
                font-size: 14px;
                border: 1px solid rgba(49, 131, 251, 0.4);
                line-height: 14px;
                padding: 15px 17px;
                min-height: 170px;
                margin-bottom: 40px;
                font-weight: 600;
                color: #3183fb;
                @media(max-width: 450px){
                    font-size: 12px;
                }
            }
            p{
                position: absolute;
                top: 15px;
                left: 17px;
                z-index: -3;
                width: auto;
                color: #3183fb;
                font-weight: 600;
                text-transform: uppercase;
                margin: 0;
                opacity: 0;

                &.description-plh{
                    opacity: 0.4;
                }
            }
        }
        .third{
            display: inline-block;
            width: calc(33% - 20px);
            margin-right: 30px;
            &:nth-of-type(3n){
                margin-right: 0;
            }
        }
        .btn{
            cursor: pointer;
            p{
                width: auto;
                font-size: 14px;
                color: #3183fb;
                font-weight: 600;
                line-height: 14px;
                text-decoration: underline;
                text-transform: uppercase;
            }
            &.for-price{
                padding-top: 7px;
                text-align: right;
                margin-right: 2px;
            }
            &.for-img{
                display: inline-block;
                padding-top: 171px;
                margin-right: 2px;
                float: right;
            }
        }
        h2{
            opacity: 0.4;
            color: #2a2a2a;
            font-size: 14px;
            font-weight: 300;
            line-height: 24px;
            margin-bottom: 40px;
        }
        .title_for_cover-wrap{
            position: relative;
            .title_for_cover{
                outline: none;
                font-size: 14px;
                border: 1px solid rgba(49, 131, 251, 0.4);
                line-height: 14px;
                padding: 15px 17px;
                width: 270px;
                min-height: 140px;
                margin-bottom: 68px;
                font-weight: 600;
                color: #3183fb;
                @media(max-width: 450px){
                    font-size: 12px;
                }
            }
            p{
                position: absolute;
                top: 15px;
                left: 17px;
                z-index: -3;
                opacity: 0;
                width: auto;
                color: #3183fb;
                font-weight: 600;
                text-transform: uppercase;
                margin: 0;
                &.title_for_cover-plh{
                    opacity: 0.4;
                }
            }
        }
        input.submit{
            width: 200px;
            height: 85px;
            border-radius: 4px;
            padding-bottom: 2px;
            padding-left: 0;
            background-image: linear-gradient(270deg, #8889f2 0%, #3183fb 100%);
            color: #ffffff;
            font-size: 14px;
            font-weight: 200;
            line-height: 82px;
            text-transform: uppercase;
            letter-spacing: 0.4px;
        }
        input.file{
            width: 0;
            position: absolute;
            opacity: 0;
            z-index: -10;
        }
        input.file+label{
            cursor: pointer;
            display: block;
            border-radius: 4px;
            border: 1px dashed rgba(49, 131, 251, 0.4);
            color: #2a2a2a;
            font-size: 14px;
            font-weight: 300;
            line-height: 12px;
            text-transform: uppercase;
            text-align: center;
            margin-bottom: 40px;
            img{
                opacity: 1;
                margin-bottom: 30px;
            }
            p{
                width: auto;
                span{
                    color: #3183fb;
                }
            }
            &.Header_Image{
                width: 100%;
                height: 156px;
                padding-top: 52px;
                padding-right: 2px;
            }
            &.Content_Image{
                width: 54%;
                height: 327px;
                padding-top: 117px;
                display: inline-block;
            }
            &.Cover_Image{
                width: 270px;
                height: 400px;
                padding-top: 144px;
                margin-bottom: 20px;
                img{
                    margin-left: 1px;
                }
                p{
                    margin-right: 12px;
                }
            }
        }
        .change-size{
            width: 270px;
            margin-bottom: 73px;
            position: relative;
            img{
                vertical-align: middle;
                &.big-size{
                    width: 11%;
                }
            }

            p{
                padding-left: 3px;
                text-transform: none;
                font-size: 14px;
                opacity: 0.4;
                color: #2a2a2a;
                margin-bottom: 23px;
            }
            .scale{
                position: relative;
                display: inline-block;
                width: 176px;
                height: 1px;
                margin-left: 22px;
                margin-right: 22px;
                background: #e8f1ff;
            }
            .point{
                position: absolute;
                top: 61%;
                left: 44.7%;
                width: 18px;
                height: 18px;
                border-radius: 50%;
                background: #e8f1ff;
                cursor: pointer;
            }
            
        }
    }
}