.subscribe{
    padding: 30px 0 32px;
    background-color: #e8f1ff;
    font-family: Helvetica;
    position: relative;
    .messadge{
        position: absolute;
        width: 65px;
        height: 65px;
        border-radius: 50%;
        background: url("../img/pics/chat.svg") no-repeat 50% 50%;
        background-color: #3183fb;
        top: -115px;
        right: 49px;
        cursor: pointer;
    }
    form{
        width: 100%;
        max-width: 1271px;
        text-align: center;
        padding-right: 0;
        margin: 0 auto;
        position: relative;
        @media(max-width: 980px){
            width: 471px;
        }
        @media(max-width: 520px){
            width: 410px;
        }
        @media(max-width: 440px){
            width: 100%;
            padding: 0 10px;
        }
        label{
            color: #2a2a2a;
            font-size: 35px;
            font-weight: 600;
            vertical-align: top;
            line-height: 38px;
            @media(max-width: 520px){
                font-size: 30px;
            }
            @media(max-width: 440px){
                font-size: 24px;
            }
        }
        .form-bl{
            display: inline-block;
            position: relative;
            @media(max-width: 440px){
                width: 100%;
            }
            input{
                font-size: 14px;
                display: inline-block;
                width: calc(90px + 30.5vw);
                margin-left: 23px;
                height: 38px;
                outline: none;
                border: none;
                border-left: 1px solid rgba(49, 131, 251, 0.4);
                border-right: 1px solid rgba(49, 131, 251, 0.4);
                background-color: #e8f1ff;
                padding: 0 57px 0 52px;
                margin-bottom: -1px;
                &:focus::placeholder{
                    color: transparent;
                }
                &::placeholder{
                    color: #2a2a2a;
                    opacity: 0.4;
                    text-transform: uppercase;
                    word-spacing: 3px;
                    color: #2a2a2a;
                    font-family: Helvetica;
                    font-size: 13px;
                    font-weight: 700;
                }
                @media(max-width: 980px){
                    display: block;
                    width: 471px;
                    margin-left: 0;
                    padding: 0 62px 0 20px;
                    margin-top: 10px;
                }
                @media(max-width: 520px){
                    width: 410px;
                }
                @media(max-width: 440px){
                    width: 100%;
                }
            }
            input.btn{
                width: 27px;
                height: 16px;
                position: absolute;
                top: 11px;
                right: 54px;
                font-size: 0;
                background-image: url("../img/pics/arrow-right.svg");
                background-repeat: no-repeat;
                padding: 0;
                border: none;
                background-color: transparent;
                @media(max-width: 980px){
                    right: 20px;
                }
            }
        }
    }
}
.page-footer{
    background-color: #2a2a2a;
    padding: 60px 30px 30px 0;
    font-weight: 300;
    text-align: center;
    @media(max-width: 940px){
        padding-right: 0;
    }
    .footer-center{
        position: relative;
        width: calc(560px + 30.2vw);
        margin: 0 auto;
        padding-top: 1px;
        
        @media(max-width: 940px){
            width: auto;
            margin: 0 30px;
        }
        @media(max-width: 770px){
            margin: 0 auto;
            width: calc(25vw + 359px);
        }
        @media(max-width: 560px){
            width: auto;
            margin: 0;
            display: inline-block;
        }
        @media(max-width: 440px){
            width: 100%;
            padding: 15px;
        }
        .inline{
            text-align: left;
            display: inline-block;
            vertical-align: top;
            color: #ffffff;
            font-family: Helvetica;
            font-size: 16px;
            @media(max-width: 440px){
                font-size: 14px;
            }
            a{
                display: block;
                color: #ffffff;
                opacity: 0.4;
                font-family: Helvetica;
                font-size: 16px;
                line-height: 19px;
                text-decoration: none;
                line-height: 21px;
                margin-bottom: 5px;
                transition: .4s ease-out;
                &:hover{
                    opacity: 1;
                }
                @media(max-width: 440px){
                    font-size: 14px;
                }
            }
            &.category{
                margin-left: 6.99vw;
                margin-right: 11.37vw;
                padding-left: 1px;
                @media(max-width: 860px){
                    margin-left: 30px;
                    margin-right: 50px;
                }
                @media(max-width: 770px){
                    margin: 0 8vw 0 13vw;
                }
                @media(max-width: 520px){
                    margin: 0 26px;
                }
                a:last-child{
                    text-decoration: underline;
                }
            }
            &.feedback{
                width: 264px;
                @media(max-width: 940px){
                    width: 220px;
                }
                @media(max-width: 560px){
                    width: 170px;
                }
                @media(max-width: 440px){
                    width: 150px;
                }
                p{
                    opacity: 0.4;
                    line-height: 26px;
                    &.head{
                        font-weight: 600;
                    }
                }
                .write-us{
                    width: 120px;
                    height: 55px;
                    margin-top: 20px;
                    border-radius: 3px;
                    // background-image: linear-gradient(270deg, #8889f2 0%, #3183fb 100%);
                    opacity: 1;
                    &.btns{
                        background: #3183fb;
                        cursor: pointer;
                        position: relative;
                        overflow: hidden;
                        z-index: 5;
                        i{
                            width: 260px;
                            height: 260px;
                            position: absolute;
                            top: -160%;
                            left: 5%;
                            pointer-events: none;
                            z-index: -1;
                            &:before{
                                content: '';
                                position: absolute;
                                top: 0;
                                left: 0;
                                right: 0;
                                bottom: 0;
                                border-radius: 50%;
                                background: radial-gradient(ellipse at center, #8889f2 10%, transparent 65%);
                                transition: 0.3s ease-in-out;
                            }
                        }
                        
                        &:hover, &:focus{
                            i{
                                &:before{
                                    transform: scale3d(1.2, 1.2, 1.2);
                                    transition: 1s cubic-bezier(0.18, 0, 0, 0.995);
                                }
                            }
                        }
                        &:active{
                         i{
                            &:before{
                                transform: scale3d(2, 2, 2);
                            }
                        }
                    }
                }
                p{
                    opacity: 1;
                    text-align: center;
                    line-height: 55px;
                    position: relative;
                    font-weight: 200;
                    font-size: 14px;
                    padding-left: 2px;
                    text-transform: uppercase;
                    letter-spacing: .4px;
                }

            }
        }
        &.social{
            margin-left: 6.9vw;
            margin-right: 4.6vw;
            @media(max-width: 770px){
                margin-right: 30px;
                margin-top: 50px;
            }
            p{
                opacity: 0.4;
                margin-bottom: 12px;
                letter-spacing: .6px;
                line-height: 14px;
            }
            .wrap-soc-link{
                height: 16px;
                margin-bottom: 29px;
                a{
                    opacity: 1;
                    display: inline-block;
                    margin-right: 5px;
                    &:hover{
                        img{
                            opacity: 1;
                        }
                    }
                    img{
                        opacity: 0.4;
                        transition: .4s ease-out;
                    }
                }
            }
            .call{
                width: 140px;
                position: relative;
                opacity: 1;
                &:hover{
                    img{
                        opacity: 1;
                    }
                    p{
                        opacity: 1;
                    }
                }
                img{
                    opacity: .4;
                    transition: .4s ease-out;
                    vertical-align: top;
                }
                p{
                 transition: .4s ease-out;
                 display: inline-block;
                 margin: 0;
                 font-size: 15px;
                 font-weight: 600;
                 line-height: 15px;
                 position: absolute;
                 top: 0;
                 right: 0;
             }
         }
     }
     &.page-up{
        position: absolute;
        right: -8px;
        &:hover{
            img{
                opacity: 1;
            }
            p{
                opacity: 1;
            }
        }
        @media(max-width: 980px){
            right: 0;
        }
        @media(max-width: 770px){
            margin-top: 50px;
            position: relative;
        }
        p{
            opacity: 0.4;
            color: #ffffff;
            font-family: Helvetica;
            font-size: 14px;
            font-weight: 700;
            line-height: 14px;
            text-transform: uppercase;
            transition: .4s ease-out;
        }
        img{
            transition: .4s ease-out;
            opacity: .4;
        }
        a{
            position: absolute;
            top: 0;
            left: 0;
            bottom: 0;
            right: 0;
            z-index: 3;
        }
    }
}
.faq{
    margin-top: 89px;
    text-align: left;
    a{
        margin-right: 15px;
        color: #ffffff;
        font-family: Helvetica;
        font-size: 14px;
        font-weight: 300;
        opacity: 0.4;
        transition: .4s ease-out;
        &:hover{
            opacity: 1;
        }
        @media(max-width: 440px){
            font-size: 13px;
        }
    }
}
}
}


@media(max-width: 440px){
    .page-footer{ 
        .footer-center{
            .navigation{
                width: calc(50% - 4px);
                a{
                    width: 90px;
                }
            }
            .category{
                width: calc(50% - 4px);
                &.inline{
                    margin: 0;
                }
                a{
                    width: 70px;
                    margin: 0 0 5px auto;
                }
            }
            .feedback{
                &.inline{
                    width: 100%;
                    margin-bottom: 40px;
                }
            }
            .social{
                &.inline{
                    float: left;
                    margin: 0;
                }
            }
            .page-up{
                &.inline{
                    float: right;
                    margin: 0;
                }
            }
            .faq{
                a{
                    font-size: 13px;
                    margin-right: 7px;
                    &:last-child{
                        margin-right: 0;
                    }
                }
                
                &.inline{
                    margin: 0;
                }
                &::before{
                    content: "";
                    width: 100%;
                    display: block;
                    clear: both;

                    margin-bottom: 89px;
                }
            }
        }
    }
}