.wrapper-stick{
    display: inline-block;
    vertical-align: top;
    width: 100%;
    .stick-w{
        white-space: normal;
        max-width: 1262px;
        margin: 0 auto;
        padding: 105px 30px 0 32px;
        margin-bottom: 261px;
        @media(max-width: 1100px){
            margin-bottom: 105px;
        }
        @media(max-width: 870px){
            padding: 105px 5px 0;
        }
        @media(max-width: 480px){
            padding: 105px 15px 0;
        }
        .stick-item{
            display: inline-block;
            max-width: 370px;
            width: calc(33.3% - 29px);
            margin:0 14px 103px 13px;
            text-align: left;
            transition: .3s;
            text-decoration: none;
            @media(max-width: 870px){
            max-width: unset;
        }
            &:hover{
                .stick-img{
                    img{
                        transform: scale(1.024);
                        
                    }
                }
            }
            @media(max-width: 860px){
                width: calc(50% - 29px);
            }

            @media(max-width: 480px){
                width: 100%;
                margin: 0 0 60px;
            }
            .stick-img{
                position: relative;
                margin-bottom: 9px;
                .stick-type{
                    position: absolute;
                    left: 25px;
                    top: 25px;
                    p{
                        padding: 14px 17px 14px 18px;
                        border-radius: 4px;
                        background-image: linear-gradient(270deg, #8889f2 0%, #3183fb 100%);
                        color: #ffffff;
                        font-family: Helvetica;
                        font-size: 14px;
                        font-weight: 400;
                        line-height: 12px;
                        text-transform: uppercase;
                    }
                }
                img{
                    z-index: -1;
                    position: relative;
                    transition: .4s;
                    width: 100%;
                    transform: scale(1);
                }
            }
            .date{
                p{
                    opacity: 0.5;
                    color: #2a2a2a;
                    font-family: Helvetica;
                    font-size: 14px;
                    font-weight: 600;
                    line-height: 14px;
                    text-transform: uppercase;
                }
            }
            .stick-name{

                p{
                    color: #2a2a2a;
                    font-family: Helvetica;
                    font-size: 25px;
                    font-weight: 600;
                    line-height: 37px;
                }
            }
        }
        .btn{
         width: 200px;
         height: 85px;
         border-radius: 4px;
         background: #3183fb;
         cursor: pointer;
         position: relative;
         overflow: hidden;
         z-index: 5;
         margin: 0 auto;
         i{
            width: 360px;
            height: 360px;
            position: absolute;
            top: -160%;
            left: 5%;
            pointer-events: none;
            z-index: 0;
            &:before{
                content: '';
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
                border-radius: 50%;
                background: radial-gradient(ellipse at center, #8889f2 10%, transparent 65%);
                transition: transform 0.3s ease-in-out;
            }
        }
        p{
            color: #fff;
            font-size: 14px;
            font-weight: 200;
            text-transform: uppercase;
            text-align: center;
            line-height: 85px;
            position: relative;
        }
        &:hover, &:focus{
            i{
                &:before{
                    transform: scale3d(1.2, 1.2, 1.2);
                    transition: 1s cubic-bezier(0.18, 0, 0, 0.995);
                }
            }
        }
        &:active{
         i{
            &:before{
                transform: scale3d(2, 2, 2);
            }
        }
    }
}
}
}